import {Injectable} from '@angular/core';
import {HttpErrorResponse} from "@angular/common/http";
import {AuthService} from "../../modules/common/modules/auth/services/auth.service";
import {NotifierService} from "../../services/notifier/notifier.service";
import {CommonBase} from "../common/base.common";
import {IAuth} from "../../modules/common/modules/auth/models/auth.model";

@Injectable({
  providedIn: 'root'
})
export class BaseService extends CommonBase {

  constructor(
    protected authService: AuthService,
    protected notifier: NotifierService
  ) {
    super();
  }

  handleError(err: HttpErrorResponse, onTokenRefreshed: (auth: IAuth) => void, other: (err: HttpErrorResponse) => void) {
    if (err.status === 401 || err.status === 0 || err.status === 504) {
      console.log("Refreshing token", err);
      this.authService.refreshToken(onTokenRefreshed);
    } else {
      console.log("Other error", err);
      other(err);
    }
  }

}
