import {Injectable} from '@angular/core';
import {IParams} from "../../../../../base/endpoints/iparams";
import {IPage} from "../../../../../base/models/IPage";
import {IPollOption} from "../../poll/models/ipoll-option";
import {WebAppEndpoint} from "../../../../webapp/web-app.endpoint";
import {FormBuilder, FormControl, Validators} from "@angular/forms";
import {BaseService} from "../../../../../base/services/base.service";
import {AuthService} from "../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {ILabel} from "../models/ilabel";
import {IQuestionnaire} from "../../questionnaires/models/IQuestionnaire";
import {IAuth} from "../../../../common/modules/auth/models/auth.model";

@Injectable({
  providedIn: 'root'
})
export class LabelService extends BaseService {

  constructor(authService: AuthService,
              notifier: NotifierService,
              private http: HttpClient,
              private formBuilder: FormBuilder,
              private eventBusService: EventBusService) {
    super(authService, notifier);
  }


  searchLabels(key: string, endpoint: string, params: IParams) {
    this.http.get<IPage<ILabel>>(WebAppEndpoint.LABELS.SEARCH(endpoint, params))
      .subscribe((label: IPage<ILabel>) => {
        this.eventBusService.publish(
          key,
          label
        )
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.searchLabels(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  updateLabel(key: string, label: ILabel) {
    this.http.patch<ILabel>(WebAppEndpoint.LABELS.UPDATE(label.id), label)
      .subscribe((label: ILabel) => {
        this.eventBusService.publish(
          key,
          label
        )
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.updateLabel(key, label)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  getLabelForm(name: string) {
    return this.formBuilder.group({
      id: new FormControl(),
      color: new FormControl(),
      parent_id: new FormControl(null),
      name: new FormControl(name, [Validators.required]),
      image: new FormControl(),
      description: new FormControl(""),
      created_at: new FormControl(),
      updated_at: new FormControl(),
      root_id: new FormControl(),
      path: new FormControl(),
      absolute_path: new FormControl(),
    })
  }

  populateLabelForm(label: ILabel) {
    return this.formBuilder.group({
      id: new FormControl(label.id),
      absolute_path: new FormControl(label.absolute_path),
      color: new FormControl(label.color),
      created_at: new FormControl(label.created_at),
      updated_at: new FormControl(label.updated_at),
      root_id: new FormControl(label.root_id),
      path: new FormControl(label.path),
      parent_id: new FormControl(label.parent_id),
      name: new FormControl(label.name),
      image: new FormControl(label.image),
      description: new FormControl(label.description),
    })
  }

  createLabel(key: string, parentCode: string | null, label: ILabel) {
    label.parent_code = parentCode
    this.http.post<ILabel>(WebAppEndpoint.LABELS.CREATE(), label)
      .subscribe((que: ILabel) => {
        this.eventBusService.publish(
          key,
          que
        )
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.createLabel(key, parentCode, label)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }
}
