import {Component, Renderer2} from '@angular/core';
import {AuthService} from "../../../common/modules/auth/services/auth.service";
import {hingeInFromTop, slideIn} from "../../../../base/animations/component.animation";
import {MENU, MenuGroup, MenuItem} from "./menu.model";
import {NavigationStart, Router} from "@angular/router";

@Component({
  selector: 'admin-left-nav',
  templateUrl: './left-nav.component.html',
  styleUrls: ['./left-nav.component.scss'],
  animations: [
    hingeInFromTop('expanded'),
    slideIn('slideIn', 'left'),
  ]
})
export class LeftNavComponent {

  isAdmin: boolean

  authorities: string[] = []

  isMenuCollapsed = false
  menu = MENU

  isTabletOrMobileScreen = window.innerWidth <= 1280
  isTabletScreen = window.innerWidth <= 1280 && window.innerWidth >= 992

  constructor(
    authService: AuthService,
    private renderer: Renderer2,
    private router: Router,
  ) {
    this.isAdmin = authService.isAdmin()
    this.authorities = authService.getAuthorities()
    this.setActiveMenuItem(this.router.url)
    this.router.events.subscribe((event) => {
      if (event instanceof NavigationStart) {
        this.setActiveMenuItem(event.url)
      }
    });
  }

  toggleLeftMenu() {
    if (!this.isTabletOrMobileScreen) {
      return
    }
    const body = document.body;

    body.classList.toggle('sidebar-enable');
    if (window.innerWidth <= 992) {
      body.classList.toggle('vertical-collpsed');
      console.log(this.isMenuCollapsed)
      this.isMenuCollapsed = !this.isMenuCollapsed
      console.log(this.isMenuCollapsed)
    }

    if (body.classList.contains('vertical-collpsed')) {
      this.renderer.addClass(body, 'vertical-collpsed');
      this.isMenuCollapsed = true
    } else {
      this.renderer.removeClass(body, 'vertical-collpsed');
      this.isMenuCollapsed = false
    }
  }

  onMenuGroupClick(groupName: string) {
    this.menu = this.menu.map(g => {
      if (g.title == groupName) {
        g.expanded = !g.expanded
      } else {
        g.expanded = false
      }
      return g
    })

    // if (window.innerWidth <= 1280) {
    //   this.expandSidebar(true)
    // }
  }

  expandSidebar(expand: boolean) {
    const body = document.body;
    console.log(this.isMenuCollapsed)
    this.isMenuCollapsed = !expand
    console.log(this.isMenuCollapsed)
    if (!expand) {
      body.classList.add('sidebar-enable');
      body.classList.add('vertical-collpsed');
    } else {
      body.classList.remove('sidebar-enable');
      body.classList.remove('vertical-collpsed');
    }

    if (this.isTabletScreen && !this.isMenuCollapsed){
      this.menu = this.menu.map(g => {
        g.expanded = true
        return g
      })
    }
  }


  private setActiveMenuItem(url: string) {
    this.menu = this.menu.map(g => {
      let items = g.items.map(i => {
        i.selected = url.includes(i.link)
        return i
      })
      g.items = items
      g.expanded = items.some(i => i.selected)
      return g
    })

    if (this.isTabletScreen) {
      this.expandSidebar(false)
    }
  }

  onMenuItemClick(item: MenuItem) {
    // this.toggleLeftMenu()
    this.router.navigateByUrl(item.link).then(r => {
    })
  }

  hasPermission(item: MenuItem) {
    return this.isAdmin || item.permissions.some(p => this.authorities.includes(p))
  }

  shouldHide(group: MenuGroup) {
    return !group.items.some(i => this.hasPermission(i))
  }
}

