export interface INotification {
  id: number;
  title: string;
  description: string;
  image: string | null;
  priority: string;
  read: boolean;
  sticky: boolean;
  created_at: Date;
  updated_at: Date;
  redirect_url: string;
  view_count: number;
  click_count: number;
  text_color: string;
  background_color: string;
  action_color: string;
  response: string;
  user_id: number;
  user_full_name: string;
}

export class INotificationReq {
  title: string | null | undefined= ""
  description: string | null = null
  response: string | null = null
  image: string | null = null
  priority: string = Priorities.LOW
  sticky: boolean = false
  redirect_url: string | null = null
  text_color: string = "#000000"
  background_color: string = "#FFFFFF"
  action_color: string = "#004885"
  user_id: number | null = null
}

export enum Priorities {
  LOW = 'LOW',
  NORMAL = 'NORMAL',
  HIGH = 'HIGH',
  URGENT = 'URGENT',
  IMMEDIATE = 'IMMEDIATE'
}

export interface INotificationResponse{
  response: string
}
