import {AfterViewInit, Component, EventEmitter, Input, Output} from '@angular/core';
import {FormControl, Validators} from "@angular/forms";
import {BaseComponent} from "../../../../../base/components/base.component";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {IPostRequest} from "../../../models/posts/IPostRequest";
import {PostService} from "../../../services/post.service";
import {IPost} from "../../../models/posts/IPost";
import {EventKey} from "../../../../../services/eventbus/event.key";
import {AuthService} from "../../../../common/modules/auth/services/auth.service";
import {IAuth} from "../../../../common/modules/auth/models/auth.model";
import {IUploadFile} from "../../../models/uploads/IUploadFile";
import {FileUploadService} from "../../../../../services/files/file-upload.service";
import {IImageUploadResponse} from "../../../../../models/IImageUploadResponse";
import {TextUtility} from "../../../../common/modules/utility/text.utility";

@Component({
  selector: 'app-post-box',
  templateUrl: './post-box.component.html',
  styleUrls: ['./post-box.component.scss']
})
export class PostBoxComponent extends BaseComponent implements AfterViewInit {
  @Input() commentBox: boolean = false
  @Input() parentId: number | null | undefined
  @Output() onPosted: EventEmitter<IPost> = new EventEmitter<IPost>()
  @Output() onCancel: EventEmitter<boolean> = new EventEmitter<boolean>()

  @Input() post: IPost | null = null

  pr: IPostRequest = {
    id: null,
    parent_id: null,
    content: '',
    images: [],
    thumbs: [],
    type: 'STATUS',
    hashtags: [],
    is_public: true
  }
  content: FormControl = new FormControl('', [Validators.required, Validators.minLength(1)])
  uploadFiles: IUploadFile[] = []

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private notifier: NotifierService,
    private postService: PostService,
    private auth: AuthService,
    private fileUploadService: FileUploadService
  ) {
    super(eventBus, router, subContainer);
  }

  protected componentName(): string {
    return "PostBoxComponent";
  }

  ngOnInit(): void {
    this.loading = false
  }

  ngAfterViewInit(): void {
    setTimeout(() => {
      if (this.post == null) return
      this.pr.id = this.post.id
      this.pr.images = this.post.images
      this.pr.thumbs = this.post.thumbs
      this.pr.content = this.post.content
      this.content.setValue(this.pr.content)
    }, 200)
  }

  uploadImages(): void {
    console.log('uploading images..')
    let files: File[] = []
    this.uploadFiles.forEach(uf => {
      files.push(uf.file)
    });
    let key = this.componentKey(EventKey.FILES.IMAGE_UPLOAD('post:' + this.pr?.id))
    this.fileUploadService.uploadImages(
      key,
      files
    )
    this.subscribe(key, null, (value, meta) => {
      this.notifier.log(meta.data)
      let imageResponses = <IImageUploadResponse[]>meta.data
      imageResponses.map(r => r.image_url).forEach(i => {
        this.pr.images.push(i)
      })
      imageResponses.map(r => r.thumb_url).forEach(t => {
        this.pr.thumbs.push(t)
      })
      this.submitPost();
    });
  }

  submit() {
    this.loading = true

    if (this.uploadFiles.length > 0) {
      this.uploadImages()
    } else {
      this.submitPost()
    }
  }

  submitPost() {
    if (!this.content.valid) {
      this.notifier.warning('Invalid!', 'Need to input a valid comment.\n' + this.content.value)
      return
    }

    this.pr.parent_id = this.parentId == null ? null : this.parentId
    this.pr.content = this.content.value
    this.pr.hashtags = TextUtility.findHashtags(this.pr.content, true)

    let key = this.componentKey(EventKey.POSTS.SUBMIT_POST(this.pr.parent_id, this.pr.content))
    if (this.pr.id == null)
      this.postService.submitPost(key, this.pr)
    else
      this.postService.updatePost(key, this.pr)
    this.subscribe(key, null, (value, meta) => {
      let post: IPost = meta.data
      this.onPosted.emit(post)
      this.content.setValue('')
      this.loading = false
    })
  }


  getAuth(): IAuth {
    return this.auth.getAuth()
  }

  onImageSelected(event: any) {
    if (this.uploadFiles.length >= 3) {
      this.notifier.info('Operation not allowed!', 'You can upload 3 photos in a post.')
      return;
    }
    if (event.target.files && event.target.files[0]) {
      // validate image
      const mimeType = event.target.files[0].type;
      if (mimeType.match(/image\/*/) == null) {
        this.notifier.warning("Unsupported", "Only images are supported.");
        return;
      }

      let uf: IUploadFile = {
        file: event.target.files[0],
        previewUrl: null
      }

      const reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (e) => { // called once readAsDataURL is completed
        let url = e.target?.result
        if (url !== undefined && url !== null) {
          uf.previewUrl = url
          this.uploadFiles.push(uf);
        }
      }
    }
  }

  removeExImage(t: string) {
    if (this.pr == null) return
    let index = this.pr.thumbs.indexOf(t)
    this.pr.thumbs.splice(index)
    this.pr.images.splice(index)
  }

  removeImage(uf: IUploadFile) {
    let index = this.uploadFiles.map(file => file.previewUrl).indexOf(uf.previewUrl)
    this.uploadFiles.splice(index, 1)
  }


}
