import {Injectable} from '@angular/core';
import {BaseService} from "../../../base/services/base.service";
import {AuthService} from "../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {WebAppEndpoint} from "../web-app.endpoint";
import {IPage} from "../../../base/models/IPage";
import {IPost} from "../models/posts/IPost";
import {EventBusService} from "../../../services/eventbus/event-bus.service";
import {IPostRequest} from "../models/posts/IPostRequest";
import {ReactionTypes} from "../models/posts/reaction-types.enum";
import {IReactionSlice} from "../models/posts/reactions/reaction-slice.model";
import {IParams} from "../../../base/endpoints/iparams";
import {IReaction} from "../models/posts/reactions/reaction.model";
import {ITag} from "../models/posts/tag.model";
import {IAuth} from "../../common/modules/auth/models/auth.model";

@Injectable({
  providedIn: 'root'
})
export class PostService extends BaseService {


  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBus: EventBusService
  ) {
    super(authService, notifier)
  }

  fetchPosts(key: string, params: IParams) {

    this.http.get<IPage<IPost>>(WebAppEndpoint.POSTS.FETCH_NEWS_FEED_POSTS(params))
      .subscribe((postPage: IPage<IPost>) => {
        this.eventBus.publish(key, postPage)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchPosts(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })

  }

  fetchPost(key: string, id: number) {
    this.http.get<IPost>(WebAppEndpoint.POSTS.FETCH_POST(id))
      .subscribe((post: IPost) => {
        this.eventBus.publish(key, post)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchPost(key, id)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchComments(key: string, postId: number) {
    this.http.get<IPage<IPost>>(WebAppEndpoint.POSTS.FETCH_COMMENTS(postId, {}))
      .subscribe((postPage: IPage<IPost>) => {
        this.eventBus.publish(key, postPage)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchComments(key, postId)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  submitPost(key: string, pr: IPostRequest): void {
    this.http.post<IPost>(WebAppEndpoint.POSTS.SUBMIT_POST(), pr)
      .subscribe((post: IPost) => {
        this.eventBus.publish(key, post)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.submitPost(key, pr)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })

  }

  updatePost(key: string, pr: IPostRequest): void {
    if (pr.id == null) return
    this.http.patch<IPost>(WebAppEndpoint.POSTS.UPDATE_POST(pr.id), pr)
      .subscribe((post: IPost) => {
        this.eventBus.publish(key, post)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.updatePost(key, pr)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })

  }

  deletePost(key: string, postId: number): void {
    this.http.delete(WebAppEndpoint.POSTS.DELETE_POST(postId))
      .subscribe(res => {
        this.eventBus.publish(key, res)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.deletePost(key, postId)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  react(key: string, postId: number, reactionType: ReactionTypes) {
    this.http.post(WebAppEndpoint.POSTS.REACT_TO_POST(postId), {post_id: postId, reaction_type: reactionType})
      .subscribe(value => {
        this.eventBus.publish(key, value)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.react(key, postId, reactionType)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchPostReactions(key: string, postId: number, params: IParams) {
    this.http.get<IReactionSlice[]>(WebAppEndpoint.POSTS.FETCH_POST_REACTIONS(postId, params))
      .subscribe((reactions: IReactionSlice[]) => {
        this.eventBus.publish(key, reactions)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchPostReactions(key, postId, params)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchReactions(key: string, params: IParams) {
    this.http.get<IPage<IReaction>>(WebAppEndpoint.POSTS.FETCH_REACTIONS(params))
      .subscribe((reactions: IPage<IReaction>) => {
        this.eventBus.publish(key, reactions)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchReactions(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchUserPosts(key: string, username: string, params: IParams) {
    this.http.get<IPage<IPost>>(WebAppEndpoint.POSTS.FETCH_USER_POSTS(username, params))
      .subscribe((posts: IPage<IPost>) => {
        this.eventBus.publish(key, posts)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchUserPosts(key, username, params)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchTrendingTags(key: string, params: IParams) {
    this.http.get<ITag[]>(WebAppEndpoint.POSTS.FETCH_TRENDING_TAGS(params))
      .subscribe((tags: ITag[]) => {
        this.eventBus.publish(key, tags)
      }, (err: HttpErrorResponse) => {
        this.eventBus.publish(this.getErrorKey(key), err)
      })
  }
}
