import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {IProduct} from "../../../models/inventory/IProduct";
import {StringUtil} from "../../../../../utils/string.util";
import {IProductSlice} from "../../../models/inventory/product-slice.model";

@Component({
  selector: 'app-single-product-item',
  templateUrl: './single-product-item.component.html',
  styleUrls: ['./single-product-item.component.scss']
})
export class SingleProductItemComponent implements OnInit {

  @Input() product: IProductSlice | null = null
  @Output() onClick: EventEmitter<IProductSlice> = new EventEmitter<IProductSlice>()

  constructor() {
  }

  ngOnInit(): void {
  }

  takeOnly(str: string): string {
    if (str.length < 25) return str
    return StringUtil.takeOnly(str, 25) + '..'
  }

  onItemClick(product: IProductSlice) {
    this.onClick.emit(product)
  }
}
