import {Injectable} from '@angular/core';
import {BaseService} from "../../../../../base/services/base.service";
import {AuthService} from "./auth.service";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {AuthEndpoint} from "../auth.endpoint";
import {TokenResponse} from "../models/token-response.model";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {EventKey} from "../../../../../services/eventbus/event.key";
import {IRegisterModel} from "../models/register.model";
import {IAuth} from "../models/auth.model";

@Injectable({
  providedIn: 'root'
})
export class RegistrationService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBus: EventBusService
  ) {
    super(authService, notifier)

  }

  sendOtp(key: string, identity: string): void {
    this.http.post<TokenResponse>(AuthEndpoint.REGISTER.SEND_OTP({identity: identity}), null)
      .subscribe((response: TokenResponse) => {
        this.eventBus.publish(key, response)
      }, error => {
        this.eventBus.publish(this.getErrorKey(key), error)
      })
  }

  register(key: string, regObj: IRegisterModel, otp: string) {
    if (regObj.email?.trim().length === 0) regObj.email = null
    this.http.post<IAuth>(AuthEndpoint.REGISTER.REGISTER({token: otp}), regObj)
      .subscribe((auth: IAuth) => {
        this.authService.setAuth(auth)
        this.eventBus.publish(key, auth)
      }, (error: HttpErrorResponse) => {
        this.eventBus.publish(this.getErrorKey(key), error)
      })
  }
}
