import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {IReview} from "../../../models/review/IReview";
import {BaseComponent} from "../../../../../base/components/base.component";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {ReviewService} from "../../../services/review.service";
import {EventKey} from "../../../../../services/eventbus/event.key";
import {IAttribute} from "../../../models/review/iAttribute";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {IUploadFile} from "../../../models/uploads/IUploadFile";
import {FileUploadService} from "../../../../../services/files/file-upload.service";
import {IImageUploadResponse} from "../../../../../models/IImageUploadResponse";
import {IRating} from "../../../models/review/IRating";
import {HttpErrorResponse} from "@angular/common/http";
import {ReviewSteps} from "../../../models/review/review-steps.enum";
import {LeaderboardService} from "../../../../common/modules/accounting/leaderboard/services/leaderboard.service";
import {ILeaderboard} from "../../../models/leaderboard/leaderboard.model";
import {AuthService} from "../../../../common/modules/auth/services/auth.service";

@Component({
    selector: 'app-post-review',
    templateUrl: './post-review.component.html',
    styleUrls: ['./post-review.component.scss']
})
export class PostReviewComponent extends BaseComponent implements OnInit {

    @Input() productId: number = 0
    @Input() stepDoneNavigateTo: string | null = null
    @Output() onReviewPosted: EventEmitter<IReview> = new EventEmitter<IReview>()

    step: ReviewSteps = ReviewSteps.RATING
    attrs: IAttribute[] = []
    uploadFiles: IUploadFile[] = []

    ratingLoading: boolean = true
    reviewSubmitProgress: number = 0
    reviewSubmitMessage: string = 'Please wait..'

    review: IReview = {
        id: null,
        photos: [],
        thumbs: [],
        title: '',
        pros: '',
        cons: '',
        content: '',
        product_id: 0,
        product_slug: '',
        ratings: [],
        weight: null,
        posted_by_name: null,
        average_rating: null,
        created_at: null,
        updated_at: null,
        post: null,
        verification_status: null,
        verified_by: null,
        verification_reconsiderable: false,
        chained: false,
        hash: '',
        product_name: '',
        slug: ''
    }

    errorMsg: string = ''

    ratingsFormGroup = new FormGroup({})
    postReviewForm = new FormGroup(
        {
            title: new FormControl(this.review.title, [
                Validators.required, Validators.minLength(4)
            ]),
            pros: new FormControl(this.review.pros, [
                Validators.required, Validators.maxLength(511)
            ]),
            cons: new FormControl(this.review.cons, [
                Validators.required, Validators.maxLength(511)
            ]),
            ratings: this.ratingsFormGroup,
        }
    );


    constructor(eventBus: EventBusService,
                router: Router,
                subContainer: SubscriptionContainerService,
                private reviewService: ReviewService,
                private notifier: NotifierService,
                private fileUploadService: FileUploadService,
                private leaderboardService: LeaderboardService,
                private auth: AuthService) {
        super(eventBus, router, subContainer);
    }

    protected componentName(): string {
        return "PostReviewComponent";
    }

    ngOnInit(): void {
        this.initFormState()
        // Load attributes
        this.loadAttrs();
    }

    private initFormState() {
        this.postReviewForm.controls['title'].setValue(this.review.title)
        this.postReviewForm.controls['pros'].setValue(this.review.pros)
        this.postReviewForm.controls['cons'].setValue(this.review.cons)
    }

    nextStep(s: string) {
        let step = s as ReviewSteps
        if (step === ReviewSteps.PROVOKE_IMAGE_UPLOAD) {
            if (this.postReviewForm.invalid)
                return
        }

        this.step = step

        this.notifier.log(this.postReviewForm.value)

        // navigate to a section to fix focus
        if (this.stepDoneNavigateTo != null)
            this.navigateByUrl(this.stepDoneNavigateTo, value => {
            })

        this.notifier.log("Step " + step + " completed.")
        this.notifier.log("Step " + (step + 1) + " started!")
    }

    prevStep(s: string) {
        this.step = s as ReviewSteps
    }

    private loadAttrs() {
        // this.productId = 2549;
        let key = this.componentKey(EventKey.ATTRS.ATTR_FOR_PRODUCT_LOADED(this.productId))
        this.reviewService.loadReviewAttributes(key, this.productId)
        this.subscribe(
            key,
            null,
            (value, meta) => {
                this.attrs = meta.data
                this.initiateReviewForm(this.attrs)
                this.ratingLoading = false
            }
        )
    }

    private initiateReviewForm(attrs: IAttribute[]) {
        attrs.forEach(attr => {
            this.ratingsFormGroup.addControl(
                String(attr.id),
                new FormControl(this.findRatingForAttr(attr.id), Validators.required)
            )
        })
    }

    findRatingForAttr(attrId: number): number {
        let v = this.review.ratings.filter(r => r.attr_id === attrId).map(r => r.value)
        if (v.length === 0) return 0
        return v[0]
    }

    updateRating(increase: boolean, attrId: number) {
        // let cg: FormGroup<number> = this.ratingsFormGroup.controls[attrId+'']
        // @ts-ignore
        let value: number = this.ratingsFormGroup.controls[attrId].value;
        if (increase) {
            if (value >= 10) {
                // @ts-ignore
                this.ratingsFormGroup.controls[String(attrId)].setValue(10)
                return
            }
            // @ts-ignore
            this.ratingsFormGroup.controls[String(attrId)].setValue(++value)
        } else {
            if (value <= 0) {
                // @ts-ignore
                this.ratingsFormGroup.controls[String(attrId)].setValue(0)
                return
            }
            // @ts-ignore
            this.ratingsFormGroup.controls[String(attrId)].setValue(--value)
        }
    }

    editRating() {
        this.step = ReviewSteps.RATING
    }

    onImageSelected(event: any) {
        if (event.target.files && event.target.files[0]) {
            // validate image
            const mimeType = event.target.files[0].type;
            if (mimeType.match(/image\/*/) == null) {
                this.notifier.warning("Unsupported", "Only images are supported.");
                return;
            }

            let uf: IUploadFile = {
                file: event.target.files[0],
                previewUrl: null
            }

            const reader = new FileReader();
            reader.readAsDataURL(event.target.files[0]);
            let previewUrl: any;
            reader.onload = (e) => { // called once readAsDataURL is completed
                let url = e.target?.result
                if (url !== undefined && url !== null) {
                    uf.previewUrl = url
                    this.uploadFiles.push(uf);
                }
            }
        }
    }

    initiatePostReview() {
        if (this.uploadFiles.length > 0) {
            this.progress(20, 'Preparing images..')
            this.uploadImages()
            return
        }
        this.progress(20, 'Submitting review..')
        this.postReview()
    }

    uploadImages(): void {
        console.log('uploading images..')
        let files: File[] = []
        this.uploadFiles.forEach(uf => {
            files.push(uf.file)
        });
        let key = this.componentKey(EventKey.FILES.IMAGE_UPLOAD(this.productId + '-review-img'))
        this.fileUploadService.uploadImages(key, files)
        this.progress(30, 'Uploading Images..')
        this.subscribe(
            key,
            null,
            (value, meta) => {
                let imageResponses = <IImageUploadResponse[]>meta.data
                imageResponses.map(r => r.image_url).forEach(i => {
                    this.review.photos.push(i)
                })
                imageResponses.map(r => r.thumb_url).forEach(t => {
                    this.review.thumbs.push(t)
                })
                this.postReview();
                this.progress(50, 'Preparing review..')
            });
    }

    progress(num: number, message: string) {
        this.reviewSubmitProgress = num
        this.reviewSubmitMessage = message
    }

    private postReview() {
        // prepare review object
        this.review.product_id = this.productId
        let title = this.postReviewForm.controls['title'].value
        this.review.title = title ? title : ""
        let pros = this.postReviewForm.controls['pros'].value
        this.review.pros = pros ? pros : ""
        let cons = this.postReviewForm.controls['cons'].value
        this.review.cons = cons ? cons : ""
        this.review.ratings = PostReviewComponent.prepareRating(this.postReviewForm.value.ratings)

        this.notifier.log("Submitting review..")
        this.submitReview(this.productId, this.review)

        this.progress(60, 'Submitting review..')
    }

    private submitReview(productId: number, review: IReview) {

        let key = this.componentKey(EventKey.REVIEWS.POST_REVIEW(productId))

        if (review.id == null)
            this.reviewService.createReview(key, this.review);
        else this.reviewService.updateReview(key, review.id, review)

        this.subscribe(key, null, (value, meta) => {
            this.reviewSubmitProgress = 100
            this.review = <IReview>meta.data
            this.notifier.log(this.review)
            // this.notifier.success("Review Submitted", "You've successfully submitted review. Thank you for your cooperation.")
            let key = this.globalKey(EventKey.REVIEWS.MY_REVIEW_POSTED(this.productId))
            this.eventBus.publish(key, this.review)
            this.nextStep(ReviewSteps.FINISHED_DIALOG)
            this.onReviewPosted.emit(this.review)

            // fetch leaderboard to update header
            this.fetchLeaderboard()
        })

        this.subscribe(this.getErrorKey(key), null, (value, meta) => {
            let error: HttpErrorResponse = meta.data
            if (error.status === 403) {
                this.nextStep(ReviewSteps.SUBMIT_ERROR)
                this.errorMsg = error.error.message
            }
        })
    }

    private static prepareRating(ratings: any): IRating[] {
        let r: IRating[] = []
        for (let key in ratings) {
            r.push({
                attr_id: parseInt(key),
                attr_name: '',
                value: ratings[key],
                id: null,
                created_at: null,
                updated_at: null
            })
        }
        // this.notifier.log('Prepared Rating')
        // this.notifier.log(ratings)
        return r
    }

    calculateAverageRating(): string {
        let ratings: IRating[] = PostReviewComponent.prepareRating(this.postReviewForm.value.ratings)
        let values = ratings.map(r => r.value)
        if (values.length == 0) return "0"
        let avg = values
            .reduce((previousValue, currentValue) => previousValue + currentValue) / ratings.length
        return avg.toFixed(1)
    }

    isStep(st: string): boolean {
        let s = st as ReviewSteps
        return this.step === s
    }

    private fetchLeaderboard() {
        let key = this.componentKey(EventKey.LEADERBOARDS.FETCH_MY_LEADERBOARD())
        this.leaderboardService.fetchLeaderboard(key, this.auth.getAuth().username)
        this.subscribe(key, null, (value, meta) => {
            let leaderboard: ILeaderboard = meta.data
            // publish result globally to update header
            let key = this.globalKey(EventKey.LEADERBOARDS.FETCH_MY_LEADERBOARD())
            this.eventBus.publish(key, leaderboard)
        })
    }


    removeImage(uf: IUploadFile) {
        let index = this.uploadFiles.map(file => file.previewUrl).indexOf(uf.previewUrl)
        this.uploadFiles.splice(index, 1)
    }

    removeExImage(t: string) {
        let index = this.review.thumbs.indexOf(t)
        this.review.thumbs.splice(index)
        this.review.photos.splice(index)
    }

    getRatingForAttr(attrId: number): number {
        // @ts-ignore
        let rating = this.ratingsFormGroup.controls[String(attrId)].value
        return rating ? rating : 0
    }
}
