import {Component, EventEmitter, Input, Output} from '@angular/core';
import {IFollower} from "../../models/users/follower.model";
import {ITabHeader} from "../tab-header/tab-header.model";
import {BaseComponent} from "../../../../base/components/base.component";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";
import {UserService} from "../../services/user.service";
import {EventKey} from "../../../../services/eventbus/event.key";
import {IPage} from "../../../../base/models/IPage";

@Component({
  selector: 'app-followers',
  templateUrl: './followers.component.html',
  styleUrls: ['./followers.component.scss']
})
export class FollowersComponent extends BaseComponent {

  @Input() isFollowing: boolean = false
  followers: IFollower[] = []
  @Output() itemClick: EventEmitter<boolean> = new EventEmitter<boolean>()

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private userService: UserService
  ) {
    super(eventBus, router, subContainer);
  }

  protected componentName(): string {
    return "FollowersComponent";
  }

  ngOnInit(): void {
    let following = this.isFollowing ? 1 : 2
    this.loadFollowers(following)
    this.registerSwipe()
  }

  getTabHeaders(): ITabHeader[] {
    let headers: ITabHeader[] = []
    headers.push({
      id: 1,
      title: 'Following'
    })

    headers.push({
      id: 2,
      title: 'Followers'
    })
    return headers
  }

  onTabSelected(tabId: number) {
    this.loadFollowers(tabId)
  }

  loadFollowers(tabId: number) {
    this.loading = true
    let following = tabId === 1
    let key = this.componentKey(EventKey.USERS.GET_MY_FOLLOWERS())
    this.userService.getFollowers(key, following)
    this.subscribe(key, null, (value, meta) => {
      let followersPage: IPage<IFollower> = meta.data
      this.followers = followersPage.content
      this.loading = false
    })
  }

  onItemClick(f: IFollower) {
    this.router.navigateByUrl('/profile', {skipLocationChange: true}).then(() => {
      this.navigate('/u/' + f.username, value => {
      });
      this.itemClick.emit(true)
    });
  }

  swipeKey = this.componentKey('swipe')

  private registerSwipe() {
    this.subscribe(this.swipeKey, null, (value, meta) => {
      let swipe = meta.data
      if (swipe === this.SWIPE_RIGHT) {
        this.isFollowing = false
        this.onTabSelected(2)
      } else {
        this.isFollowing = true
        this.onTabSelected(1)
      }
    })
  }
}
