import {Injectable} from '@angular/core';
import {BaseService} from "../../../base/services/base.service";
import {AuthService} from "../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../services/notifier/notifier.service";
import {IPage} from "../../../base/models/IPage";
import {ICategory} from "../models/inventory/ICategory";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {WebAppEndpoint} from "../web-app.endpoint";
import {EventBusService} from "../../../services/eventbus/event-bus.service";
import {IParams} from "../../../base/endpoints/iparams";
import {IInterest} from "../models/interests/iInterest";
import {IAttribute} from "../models/review/iAttribute";
import {IAuth} from "../../common/modules/auth/models/auth.model";

@Injectable({
  providedIn: 'root'
})
export class CategoryService extends BaseService {

  constructor(authService: AuthService,
              notifier: NotifierService,
              private http: HttpClient,
              private eventBusService: EventBusService) {
    super(authService, notifier);
  }

  searchCategories(key: string, params: IParams){
    this.http.get<IPage<ICategory>>(
      WebAppEndpoint.CATEGORIES.SEARCH(params)
    ).subscribe((categoryPage: IPage<ICategory>) => {
      this.eventBusService.publish(key, categoryPage)
    }, (error: HttpErrorResponse) => {
      this.notifier.log(error)
    })
  }

  loadCategories(key: string, parentId: number | null, query: string | null) {
    if (query == null) query = ''
    let params: IParams = parentId == null ? {q: query} : {parent_id: parentId, q: query}
    this.http.get<IPage<ICategory>>(
      WebAppEndpoint.CATEGORIES.SEARCH(params)
    ).subscribe((categoryPage: IPage<ICategory>) => {
      this.eventBusService.publish(key, categoryPage)
    }, (error: HttpErrorResponse) => {
      this.notifier.log(error)
    })
  }

  saveInterests(key: string, selectedCategories: ICategory[]) {
    let categoryIds: number[] = selectedCategories.map(value => value.id)
    this.http.post<IInterest[]>(
      WebAppEndpoint.CATEGORIES.SAVE_INTERESTS(),
      {category_ids: categoryIds}
    )
      .subscribe((interests: IInterest[]) => {
        this.eventBusService.publish(key, interests)
      }, (error: HttpErrorResponse) => {
         this.handleError(error, (auth: IAuth) => {
          this.saveInterests(key, selectedCategories)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchInterests(key: string) {
    this.http.get<IInterest[]>(WebAppEndpoint.CATEGORIES.FETCH_INTERESTS())
      .subscribe((interests: IInterest[]) => {
        this.eventBusService.publish(key, interests)
      }, (error: HttpErrorResponse) => {
        this.notifier.log(error)
      })

  }

  loadAttributes(key: string, categoryIds: number[]) {
    this.http.get<IAttribute[]>(WebAppEndpoint.CATEGORIES.FETCH_ATTRIBUTES({category_ids: categoryIds}))
      .subscribe((attrs: IAttribute[]) => {
        this.eventBusService.publish(key, attrs)
      }, (error: HttpErrorResponse) => {
        this.notifier.log(error)
      })
  }
}
