import {Component} from '@angular/core';
import {BaseComponent} from "../../../../base/components/base.component";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";
import {IReview} from "../../models/review/IReview";
import {ReviewService} from "../../services/review.service";
import {EventKey} from "../../../../services/eventbus/event.key";
import {Meta, Title} from "@angular/platform-browser";

@Component({
  selector: 'app-review-details',
  templateUrl: './review-details.component.html',
  styleUrls: ['./review-details.component.scss']
})
export class ReviewDetailsComponent extends BaseComponent {
  // productId: number = 0
  // reviewId: number = 0
  productSlug: string = ''
  reviewSlug: string = ''
  postId: number = 0
  review: IReview | null = null

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    route: ActivatedRoute,
    private reviewService: ReviewService,
    private seoTitle: Title,
    private seoMeta: Meta
  ) {
    super(eventBus, router, subContainer);
    this.productSlug = route.snapshot.params['productSlug']
    this.reviewSlug = route.snapshot.params['reviewSlug']
    this.postId = route.snapshot.params['postId']
  }

  protected componentName(): string {
    return "ReviewDetailsComponent";
  }

  ngOnInit(): void {
    let key = this.componentKey(EventKey.REVIEWS.LOAD_REVIEW(this.reviewSlug))
    this.reviewService.fetchReview(key, this.productSlug,this.reviewSlug)
    this.subscribe(key, null, (value, meta) => {
      this.review = meta.data
      this.resolveSeo(this.review)
      this.loading = false
    })
  }


  private resolveSeo(review: IReview | null) {
    if (review == null) return

    super.initSeoItems(
      this.seoTitle,
      this.seoMeta,
      review.title,
      review.pros + ', ' + review.cons,
      [review.product_name + ' review', 'Rating for ' + review.product_name],
      null,
      null,
      this.getCoverPhoto(review),
      this.getPermalink()
    )
  }

  getCoverPhoto(review: IReview): string | null {
    if (review.photos == null || review.photos.length == 0) return null
    return review.photos[0]
  }
}
