import {Component} from '@angular/core';
import {BaseComponent} from "../../../../../../base/components/base.component";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {RegistrationService} from "../../services/registration.service";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {UserService} from "../../services/user.service";
import {AuthService} from "../../services/auth.service";
import {IAuth} from "../../models/auth.model";

@Component({
  selector: 'app-user-delete',
  templateUrl: './user-delete.component.html',
  styleUrls: ['./user-delete.component.scss']
})
export class UserDeleteComponent extends BaseComponent {

  username: string | null = null


  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private notifier: NotifierService,
    private userService: UserService,
    private authService: AuthService
  ) {
    super(eventBus, router, subContainer)
    if (this.username == null)
      this.username = this.getAuthUsername()
  }

  protected componentName(): string {
    return "UserDeleteComponent";
  }

  ngOnInit(): void {
    if (this.username == null)
      this.username = this.getAuthUsername()
  }

  getAuthUsername(): string {
    return this.authService.getAuth().username
  }


  deleteMyAccount() {
    let conf = confirm("Are you sure to continue?")
    if (!conf) return
    if(!this.authService.isAuthenticated()){
      this.router.navigate(['/login'])
      return;
    }
    let key = this.componentKey(EventKey.USERS.DELETE_ME())
    this.userService.deleteAccount(key)
    this.subscribe(key, "/login", (value, meta) => {
      this.notifier.success('Deleted!', 'Your account is deleted.')
      this.authService.logout()
      this.reloadRoute()
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let err: HttpErrorResponse = meta.data
      this.notifier.error('Error!', err.error.message)
    })
  }
}
