import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {ReactionTypes} from "../../../models/posts/reaction-types.enum";
import {BaseComponent} from "../../../../../base/components/base.component";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {PostService} from "../../../services/post.service";
import {EventKey} from "../../../../../services/eventbus/event.key";
import {NotifierService} from "../../../../../services/notifier/notifier.service";

@Component({
  selector: 'app-reaction',
  templateUrl: './reaction.component.html',
  styleUrls: ['./reaction.component.scss']
})
export class ReactionComponent extends BaseComponent {

  @Input() postId: number | null | undefined = null
  @Output() onReacted: EventEmitter<ReactionTypes> = new EventEmitter<ReactionTypes>()

  reactionPostId: number = 0

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private postService: PostService,
    private notifier: NotifierService
  ) {
    super(eventBus, router, subContainer);
  }

  protected componentName(): string {
    return "ReactionComponent";
  }

  ngOnInit(): void {
    this.reactionPostId = 0
  }

  onReactionItemClick(reaction: string) {
    if (this.postId == null) return
    let rt = ReactionTypes[reaction as keyof typeof ReactionTypes]

    let key = this.componentKey(EventKey.POSTS.SUBMIT_REACTION(this.postId))
    this.postService.react(key, this.postId, rt)
    let sub = this.subscribe(key, null, (value, meta) => {
      this.onReacted.emit(rt)
      sub?.unsubscribe()
      this.reactionPostId = this.postId == null ? 0 : this.postId
      setTimeout(() => {
        this.reactionPostId = 0
      }, 5000)
    })
    this.notifier.log('Reaction submitted: ' + rt)
  }

}
