import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {AuthService} from "../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {BaseComponent} from "../../../../../base/components/base.component";
import {ProductService} from "../../../services/product.service";
import {EventKey} from "../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {IProductSlice} from "../../../models/inventory/product-slice.model";
import {TimeUtility} from "../../../../common/modules/utility/time.utility";
import {IParams} from "../../../../../base/endpoints/iparams";

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.scss']
})
export class ProductListComponent extends BaseComponent {
  @Input() title: string = ""
  @Input() attrId: number | null = null;
  @Output() productsLoaded: EventEmitter<boolean> = new EventEmitter<boolean>()
  products: IProductSlice[] = []

  period = 'THIS MONTH'
  showMenubar: boolean = false

  constructor(private authService: AuthService,
              private notifier: NotifierService,
              eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private productService: ProductService) {
    super(eventBus, router, subContainer)
  }

  protected componentName(): string {
    return "ProductListComponent";
  }

  ngOnInit(): void {
    let dateRange = TimeUtility.calculateRange('THIS_MONTH')
    this.fetchTopProducts(
      {
        attribute_id: this.attrId,
        from_date: dateRange?.fromDate,
        to_date: dateRange?.toDate
      }
    )

  }

  onProductItemClick(product: IProductSlice) {
    this.navigate("products/" + product.product_id + "/" + product.product_slug, value => {
      console.log("Product Details: " + product.product_slug)
    })
  }

  onMenuClick(event: MouseEvent) {
    this.showMenubar = !this.showMenubar
  }

  onMenuItemClick(period: string) {
    this.showMenubar = false
    this.period = period.replace('_', ' ')
    let dateRange = TimeUtility.calculateRange(period)
    this.fetchTopProducts(
      {
        attribute_id: this.attrId,
        from_date: dateRange?.fromDate,
        to_date: dateRange?.toDate
      }
    )
  }

  private fetchTopProducts(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.PRODUCTS.PRODUCTS_LOADED(this.attrId))
    this.productService.fetchTopProducts(key, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.products = meta.data
        this.loading = false
        this.productsLoaded.emit(true)
        // this.notifier.log(meta.data);
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.log(meta.data)
    })
  }

}


