import {Component, OnInit} from '@angular/core';
import {Step} from "../../models/step.enum";
import {AsyncValidatorFn, FormControl, FormGroup, Validators} from "@angular/forms";
import {BaseComponent} from "../../../../../../base/components/base.component";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {RegistrationService} from "../../services/registration.service";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {IRegisterModel} from "../../models/register.model";
import {Genders} from "../../models/enums/genders.enum";
import {HttpErrorResponse} from "@angular/common/http";
import {IAuth} from "../../models/auth.model";

@Component({
    selector: 'app-registration',
    templateUrl: './registration.component.html',
    styleUrls: ['./registration.component.scss']
})
export class RegistrationComponent extends BaseComponent implements OnInit {

    step: Step = Step.INIT_VERIFICATION
    phone = new FormControl('', [
        Validators.required,
        Validators.minLength(11),
        Validators.maxLength(11)
    ]);
    otp = new FormControl('', [Validators.required, Validators.minLength(6)]);

    registrationForm = new FormGroup(
        {
            name: new FormControl('',
                [Validators.required, Validators.minLength(3)]
            ),
            gender: new FormControl('', [Validators.required]),
            email: new FormControl('',
                [Validators.email]
            ),
            password: new FormControl('', [
                Validators.required, Validators.minLength(6)
            ]),
            confirmPassword: new FormControl('', [
                Validators.required, Validators.minLength(6)
            ]),
            username: new FormControl('',
                [Validators.required, Validators.minLength(6)]
            )
        }
    );

    constructor(
        eventBus: EventBusService,
        router: Router,
        subContainer: SubscriptionContainerService,
        private notifier: NotifierService,
        private registrationService: RegistrationService
    ) {
        super(eventBus, router, subContainer)
    }

    protected componentName(): string {
        return "RegistrationComponent";
    }

    ngOnInit(): void {
        this.registerSubscriptions()
    }

    isVerified(): boolean {
        return this.step === Step.VERIFIED
    }

    isInitiating(): boolean {
        return this.step === Step.INIT_VERIFICATION
    }

    isWaitingForCode(): boolean {
        return this.step === Step.WAITING_FOR_CODE
    }

    sendOtp() {
        if (this.phone.invalid || this.phone.value == null) {
            this.notifier.warning('Warning', 'Phone number should be 11 digits!')
            return
        }
        let p: string = this.phone.value
        this.notifier.log(p)
        // if (!p.startsWith('+880'))
        //   p='+880'.concat(p)
        // if (p.startsWith('0') && !p.startsWith('+880'))
        //   p='+88'.concat(p)

        let key = this.componentKey(EventKey.REGISTRATION.OTP_SENT)
        this.registrationService.sendOtp(key, p)
        this.subscribe(key, null, (value, meta) => {
            this.registrationForm.controls['username'].setValue(this.phone.value)
            this.notifier.success('OTP Sent!', 'We\'ve sent you an OTP. You need to provide the OTP here.')
            this.notifier.log(meta)
            this.step = Step.WAITING_FOR_CODE
        })
        this.subscribe(this.getErrorKey(key), null, (value, meta) => {
            let error: HttpErrorResponse = meta.data
            this.notifier.warning('Failed', error.error.message)
        })
        // this.registrationForm.controls['username'].setValue(this.phone.value)
        this.notifier.log(this.phone.value)
    }


    private registerSubscriptions() {
        this.onRegisterEvent()
    }

    private onRegisterEvent() {

    }


    verifyOtp() {
        if (this.otp.invalid) {
            return
        }
        this.step = Step.VERIFIED
    }

    register() {
        if (this.registrationForm.invalid) {
            this.notifier.warning('Incorrect inputs!', 'Please fill the form correctly.')
            return
        }
        if (this.otp.value==null){
            this.notifier.warning('OTP Invalid!', 'Please enter OTP.')
            return
        }
        let regObj: IRegisterModel = this.prepare(this.registrationForm.value)
        let key = this.componentKey(EventKey.REGISTRATION.REGISTER_SUCCESS)
        this.registrationService.register(key, regObj, this.otp.value)
        this.subscribe(
            key,
            '/choose-interests',
            (value, meta) => {
                let auth: IAuth = meta.data
                this.notifier.success('SUCCESS!', "You've been successfully registered!")
            })

        this.subscribe(
            this.getErrorKey(key),
            '/sign-up',
            (value, meta) => {
                let error: HttpErrorResponse = meta.data
                this.notifier.warning('Registration Failed!', error.error.message)
            })
    }

    private prepare(value: any): IRegisterModel {
        let gender: Genders = <Genders>value.gender
        return {
            email: value.email,
            gender: gender,
            name: value.name,
            password: value.password,
            phone: this.phone.value!!,
            role: 'User',
            username: value.username
        }
    }


}
