import {Injectable} from '@angular/core';
import {BaseService} from "../../../base/services/base.service";
import {AuthService} from "../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../services/eventbus/event-bus.service";
import {IFollowStats} from "../models/users/follow-stats.model";
import {WebAppEndpoint} from "../web-app.endpoint";
import {IFollow} from "../models/users/follow.model";
import {IFollower} from "../models/users/follower.model";
import {IPage} from "../../../base/models/IPage";
import {IProfile} from "../models/users/profile.model";
import {IProfileReq} from "../models/users/profile-req.model";
import {IAuth} from "../../common/modules/auth/models/auth.model";

@Injectable({
  providedIn: 'root'
})
export class UserService extends BaseService {

  constructor(authService: AuthService,
              notifier: NotifierService,
              private http: HttpClient,
              private eventBus: EventBusService) {
    super(authService, notifier);
  }

  fetchFollowStats(key: string, username: string) {
    this.http.get<IFollowStats>(WebAppEndpoint.USER.FETCH_FOLLOW_STATS(username))
      .subscribe((fs: IFollowStats) => {
        this.eventBus.publish(key, fs)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchFollowStats(key, username)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  followUser(key: string, username: string, message: string) {
    this.http.post<IFollow>(WebAppEndpoint.USER.FOLLOW_A_USER(username), {message: message})
      .subscribe((f: IFollow) => {
        this.eventBus.publish(key, f)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.followUser(key, username, message)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  unfollowUser(key: string, username: string) {
    this.http.post<IFollow>(WebAppEndpoint.USER.UNFOLLOW_A_USER(username), null)
      .subscribe((f: any) => {
        this.eventBus.publish(key, f)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.unfollowUser(key, username)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  getFollowers(key: string, following: boolean) {
    let endpoint = following ? WebAppEndpoint.USER.GET_MY_FOLLOWING() : WebAppEndpoint.USER.GET_MY_FOLLOWERS()
    this.http.get<IPage<IFollower>>(endpoint)
      .subscribe((followers: IPage<IFollower>) => {
        this.eventBus.publish(key, followers)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.getFollowers(key, following)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  getMyProfile(key: string) {
    this.http.get<IProfile>(WebAppEndpoint.USER.GET_MY_PROFILE())
      .subscribe((profile: IProfile) => {
        this.eventBus.publish(key, profile)
      }, (err: HttpErrorResponse) => {
        this.handleError(err, (auth: IAuth) => {
          this.getMyProfile(key)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  updateProfile(key: string, profile: IProfileReq) {
    this.http.patch<IProfile>(WebAppEndpoint.USER.UPDATE_MY_PROFILE(), profile)
      .subscribe((profile: IProfile) => {
        this.eventBus.publish(key, profile)
      }, (err: HttpErrorResponse) => {
        this.handleError(err, (auth: IAuth) => {
          this.updateProfile(key, profile)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }
}
