import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {ITabHeader} from "./tab-header.model";
import {BaseComponent} from "../../../../base/components/base.component";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";

@Component({
  selector: 'app-tab-header',
  templateUrl: './tab-header.component.html',
  styleUrls: ['./tab-header.component.scss']
})
export class TabHeaderComponent extends BaseComponent {

  @Input() tabHeaders: ITabHeader[] = []
  @Input() selectedTabId: number = 0

  @Output() onSelect: EventEmitter<number> = new EventEmitter<number>()

  @Input() panelClass: string = 'active'

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService
  ) {
    super(eventBus, router, subContainer);
  }

  protected componentName(): string {
    return "TabHeaderComponent";
  }

  ngOnInit(): void {
  }


  onClick(selectedTab: ITabHeader) {
    this.selectedTabId = selectedTab.id
    this.onSelect.emit(selectedTab.id)
  }

  trackbyFn(index: number, item: any) {
    // console.log(index)
    return index;
  }
}
