import {Component, Input, OnInit} from '@angular/core';
import {IReview} from "../../../models/review/IReview";
import {BaseComponent} from "../../../../../base/components/base.component";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {IPage} from "../../../../../base/models/IPage";
import {ReviewService} from "../../../services/review.service";
import {EventKey} from "../../../../../services/eventbus/event.key";
import {FormControl} from "@angular/forms";
import {IParams} from "../../../../../base/endpoints/iparams";

@Component({
    selector: 'app-review-list',
    templateUrl: './review-list.component.html',
    styleUrls: ['./review-list.component.scss']
})
export class ReviewListComponent extends BaseComponent {
    @Input() productId: number = 0
    @Input() username: string | null = null

    reviews: IPage<IReview> | null = null

    verificationFilter = new FormControl('')
    ratingFilter = new FormControl('')
    sortFilter = new FormControl('')
    fromDateFilter = new FormControl('')
    toDateFilter = new FormControl('')

    constructor(
        protected override eventBus: EventBusService,
        protected override router: Router,
        protected override subContainer: SubscriptionContainerService,
        private reviewService: ReviewService
    ) {
        super(eventBus, router, subContainer);
    }

    protected componentName(): string {
        return "ReviewListComponent";
    }

    ngOnInit(): void {

        this.fetchReviews({})

    }


    private fetchReviews(params: IParams) {
        if (this.productId != null && this.productId > 0)
            this.fetchProductReviews(params)
        else if (this.username != null)
            this.fetchUserReviews(params)
    }

    private fetchProductReviews(params: IParams) {
        this.loading = true
        let key = this.componentKey(EventKey.REVIEWS.LOADED_PRODUCT_REVIEWS(this.productId))
        this.reviewService.fetchProductReviews(key, this.productId, params)
        this.subscribe(key, null, (value, meta) => {
            this.reviews = meta.data
            this.loading = false
        })
    }

    private fetchUserReviews(params: IParams) {
        if (this.username == null) return
        this.loading = true
        let key = this.componentKey(EventKey.REVIEWS.FETCH_USER_REVIEWS(this.username))
        this.reviewService.fetchUserReviews(key, this.username, params)
        this.subscribe(key, null, (value, meta) => {
            this.reviews = meta.data
            this.loading = false
        })
    }

    onFilterChange(event: Event) {
        this.fetchReviews({
            verification_status: this.verificationFilter.value == null ? '' : this.verificationFilter.value,
            rating: this.ratingFilter.value == null ? '' : this.ratingFilter.value,
            sort_direction: this.sortFilter.value == null ? '' : this.sortFilter.value,
            from_date: this.fromDateFilter.value === '' ? '' : new Date(this.fromDateFilter.value ? this.fromDateFilter.value : ''),
            to_date: this.toDateFilter.value === '' ? '' : new Date(this.toDateFilter.value ? this.toDateFilter.value : '')
        })
    }
}
