import {Injectable} from '@angular/core';
import {BaseService} from "../../../base/services/base.service";
import {AuthService} from "../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../services/eventbus/event-bus.service";
import {IAttribute} from "../models/review/iAttribute";
import {WebAppEndpoint} from "../web-app.endpoint";
import {IReview} from "../models/review/IReview";
import {IPage} from "../../../base/models/IPage";
import {IParams} from "../../../base/endpoints/iparams";
import {IReviewSlice} from "../models/review/review-slice.model";
import {IReviewVerification} from "../models/review/review-verification.model";
import {IReviewVerificationReq} from "../models/review/review-verification-req.model";
import {IAuth} from "../../common/modules/auth/models/auth.model";

@Injectable({
  providedIn: 'root'
})
export class ReviewService extends BaseService {

  constructor(authService: AuthService,
              notifier: NotifierService,
              private http: HttpClient,
              private eventBus: EventBusService) {
    super(authService, notifier);
  }

  fetchRecentReviews(key: string, params: IParams) {
    this.http.get<IPage<IReviewSlice>>(WebAppEndpoint.REVIEWS.FETCH_RECENT_REVIEW(params))
      .subscribe((reviews: IPage<IReviewSlice>) => {
        this.eventBus.publish(key, reviews)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchRecentReviews(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  loadReviewAttributes(key: string, productId: number): void {
    this.notifier.log("Loading attrs for product: " + productId)
    this.http.get<IAttribute[]>(WebAppEndpoint.ATTRIBUTES.GET_ATTRS_FOR_PRODUCT(productId, {}))
      .subscribe((attributes: IAttribute[]) => {
        this.eventBus.publish(
          key,
          attributes
        )
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.loadReviewAttributes(key, productId)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  createReview(key: string, review: IReview) {
    this.http.post<IReview>(WebAppEndpoint.REVIEWS.CREATE_REVIEW({}), review)
      .subscribe((review: IReview) => {
        this.eventBus.publish(key, review)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.createReview(key, review)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  updateReview(key: string, reviewId: number, review: IReview) {
    this.http.patch<IReview>(WebAppEndpoint.REVIEWS.UPDATE_REVIEW(reviewId, {}), review)
      .subscribe((review: IReview) => {
        this.eventBus.publish(key, review)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.updateReview(key, reviewId, review)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchMyReview(key: string, productId: number) {
    this.http.get<IReview>(
      WebAppEndpoint.REVIEWS.FETCH_MY_REVIEW(productId))
      .subscribe((review: IReview) => {
        this.eventBus.publish(key, review)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchMyReview(key, productId)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchReview(key: string, productSlug: string, reviewSlug: string): string {
    this.http.get<IReview>(
      WebAppEndpoint.REVIEWS.FETCH_REVIEW(productSlug, reviewSlug))
      .subscribe((review: IReview) => {
        this.eventBus.publish(key, review)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchReview(key, productSlug, reviewSlug)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
    return key
  }


  fetchProductReviews(key: string, productId: number, params: IParams) {
    this.http.get<IPage<IReview>>(
      WebAppEndpoint.REVIEWS.FETCH_PRODUCT_REVIEWS(productId, params), {}
    ).subscribe((reviews: IPage<IReview>) => {
      this.eventBus.publish(key, reviews)
    }, (error: (HttpErrorResponse)) => {
      this.handleError(error, (auth: IAuth) => {
        this.fetchProductReviews(key, productId, params)
      }, (err: HttpErrorResponse) => {
        this.eventBus.publish(this.getErrorKey(key), err)
      })
    })
  }

  fetchUserReviews(key: string, username: string, params: IParams) {
    this.http.get<IPage<IReview>>(
      WebAppEndpoint.REVIEWS.FETCH_USER_REVIEWS(username, params), {}
    ).subscribe((reviews: IPage<IReview>) => {
      this.eventBus.publish(key, reviews)
    }, (error: (HttpErrorResponse)) => {
      this.handleError(error, (auth: IAuth) => {
        this.fetchUserReviews(key, username, params)
      }, (err: HttpErrorResponse) => {
        this.eventBus.publish(this.getErrorKey(key), err)
      })
    })
  }

  verifyReview(key: string, verificationReq: IReviewVerificationReq) {
    this.http.post<IReviewVerification>(WebAppEndpoint.REVIEWS.SEND_VERIFICATION_REQUEST(), verificationReq)
      .subscribe((verification: IReviewVerification) => {
        this.eventBus.publish(key, verification)
      }, (err: HttpErrorResponse) => {
        this.handleError(err, (auth: IAuth) => {
          this.verifyReview(key, verificationReq)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  getVerificationForReview(key: string, reviewId: number) {
    this.http.get<IReviewVerification>(WebAppEndpoint.REVIEWS.GET_VERIFICATION_FOR_REVIEW(reviewId))
      .subscribe((verification: IReviewVerification) => {
        this.eventBus.publish(key, verification)
      }, (err: HttpErrorResponse) => {
        this.handleError(err, (auth: IAuth) => {
          this.getVerificationForReview(key, reviewId)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

  reconsiderVerification(key: string, verificationId: number) {
    this.http.patch<IReviewVerification>(WebAppEndpoint.REVIEWS.RECONSIDER_VERIFICATION(verificationId), null)
      .subscribe((verification: IReviewVerification) => {
        this.eventBus.publish(key, verification)
      }, (err: HttpErrorResponse) => {
        this.handleError(err, (auth: IAuth) => {
          this.reconsiderVerification(key, verificationId)
        }, (err: HttpErrorResponse) => {
          this.eventBus.publish(this.getErrorKey(key), err)
        })
      })
  }

}
