import {Injectable} from '@angular/core';
import {BaseService} from "../../../base/services/base.service";
import {AuthService} from "../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../services/notifier/notifier.service";
import {IProduct} from "../models/inventory/IProduct";
import {IPage} from "../../../base/models/IPage";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {WebAppEndpoint} from "../web-app.endpoint";
import {EventBusService} from "../../../services/eventbus/event-bus.service";
import {IRating} from "../models/review/IRating";
import {IParams} from "../../../base/endpoints/iparams";
import {IBrand} from "../models/inventory/brand.model";
import {IProductSlice} from "../models/inventory/product-slice.model";
import {IAuth} from "../../common/modules/auth/models/auth.model";

@Injectable({
  providedIn: 'root'
})
export class ProductService extends BaseService {

  constructor(authService: AuthService,
              notifier: NotifierService,
              private http: HttpClient,
              private eventBusService: EventBusService) {
    super(authService, notifier);
  }

  search(key: string, params: IParams) {
    this.http.get<IPage<IProduct>>(WebAppEndpoint.PRODUCTS.SEARCH(params))
      .subscribe((products: IPage<IProduct>) => {
        this.eventBusService.publish(
          key,
          products
        )
      }, error => {
         this.handleError(error, (auth: IAuth) => {
          this.search(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })

  }

  searchBrands(key: string, params: IParams) {
    this.http.get<IPage<IBrand>>(WebAppEndpoint.PRODUCTS.SEARCH_BRANDS(params))
      .subscribe((brands: IPage<IBrand>) => {
        this.eventBusService.publish(
          key,
          brands
        )
      }, error => {
         this.handleError(error, (auth: IAuth) => {
          this.searchBrands(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })

  }

  fetchTopProducts(key: string, params: IParams) {
    this.http.get<IProductSlice[]>(WebAppEndpoint.PRODUCTS.GET_TOP_PRODUCTS(params), {})
      .subscribe((products: IProductSlice[]) => {
        this.eventBusService.publish(
          key,
          products
        )
      }, error => {
         this.handleError(error, (auth: IAuth) => {
          this.fetchTopProducts(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchDetails(key: string, slug: string) {
    this.http.get<IProduct>(WebAppEndpoint.PRODUCTS.GET_DETAILS(slug), {})
      .subscribe((product: IProduct) => {
        this.eventBusService.publish(key, product)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchDetails(key, slug)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchAvgRatings(key: string, productId: number) {
    this.http.get<IRating[]>(WebAppEndpoint.REVIEWS.FETCH_AVG_RATINGS(productId), {})
      .subscribe((ratings: IRating[]) => {
        this.eventBusService.publish(key, ratings)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchAvgRatings(key, productId)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

}
