import {Component} from '@angular/core';
import {AuthService} from "../../modules/common/modules/auth/services/auth.service";
import {NavigationEnd, Router} from "@angular/router";
import {NotifierService} from "../../services/notifier/notifier.service";
import {EventBusService} from "../../services/eventbus/event-bus.service";
import {EventKey} from "../../services/eventbus/event.key";
import {BaseComponent} from "../../base/components/base.component";
import {SubscriptionContainerService} from "../../base/services/subscription-container.service";
import {ILeaderboard} from "../../modules/webapp/models/leaderboard/leaderboard.model";
import {IAuth} from "../../modules/common/modules/auth/models/auth.model";
import {IMenuItem} from "../../modules/webapp/components/menubox/menu-item.model";
import {MatDialog} from "@angular/material/dialog";
import {IWallet} from "../../modules/common/modules/accounting/wallet/models/wallet.model";
import {NotificationService} from "../../modules/common/modules/social/notifications/services/notification.service";
import {LeaderboardService} from "../../modules/common/modules/accounting/leaderboard/services/leaderboard.service";
import {LeaderboardComponent} from "../../modules/common/modules/accounting/leaderboard/leaderboard.component";
import {WalletService} from "../../modules/common/modules/accounting/wallet/services/wallet.service";

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent extends BaseComponent {
  notificationCount: number = 0
  leaderboard: ILeaderboard | null = null
  wallet: IWallet | null = null
  showMenubar: boolean = false

  constructor(
    private authService: AuthService,
    private notifier: NotifierService,
    router: Router,
    eventBus: EventBusService,
    subContainer: SubscriptionContainerService,
    private leaderboardService: LeaderboardService,
    private walletService: WalletService,
    private dialog: MatDialog,
    private notificationService: NotificationService
  ) {
    super(eventBus, router, subContainer);
    router.events.subscribe((value) => {
      if (value instanceof NavigationEnd) { // navigation changed
        this.registerSubscriptions()
        this.reloadData()
      }
    });
  }

  protected componentName(): string {
    return "HeaderComponent";
  }

  ngOnInit(): void {
    this.reloadData()
  }

  private reloadData() {
    this.fetchLeaderboard()
  }

  private registerSubscriptions() {

    // Logout Event
    this.subscribe(this.globalKey(EventKey.AUTH.LOGOUT), null, (value, meta) => {
      this.router.navigateByUrl("/login").then(r => {
        this.leaderboard = null
        this.wallet = null
        this.notificationCount = 0
      })
    })

    // Notification Count Event
    this.subscribe(this.globalKey(EventKey.NOTIFICATIONS.COUNT_UNREAD()), null, (value, meta) => {
      this.countUnreadNotifications()
    })

  }

  private countUnreadNotifications() {
    let key = this.componentKey(EventKey.NOTIFICATIONS.COUNT_UNREAD())
    this.notificationService.countUnread(key)
    this.subscribe(key, null, (value, meta) => {
      this.notificationCount = meta.data
    })
  }

  private fetchLeaderboard() {
    let key = this.globalKey(EventKey.LEADERBOARDS.FETCH_MY_LEADERBOARD())
    if (this.authenticated())
      this.leaderboardService.fetchLeaderboard(key, this.authService.getAuth().username)
    this.subscribe(key, null, (value, meta) => {
      this.leaderboard = meta.data
      this.fetchWallet()
      this.countUnreadNotifications()
    })
  }

  fetchWallet() {
    let key = this.componentKey(EventKey.WALLET.FETCH_MY_WALLET())
    if (this.authenticated())
      this.walletService.fetchWallet(key, {})
    this.subscribe(key, null, (value, meta) => {
      this.wallet = meta.data
    })
  }


  authenticated() {
    return this.authService.isAuthenticated();
  }

  getAuth(): IAuth {
    return this.authService.getAuth()
  }

  getUserTitle(): string {
    if (!this.authService.isAuthenticated())
      return 'Profile'
    return this.authService.getFirstName()
  }

  getStyleAttrForProgress(): string {
    return 'width: ' + this.leaderboard?.completed_percentage + '%'
  }

  getNextThresholdPoints() {
    if (this.leaderboard?.next_badge == null) return 0
    return this.leaderboard.next_badge.threshold_points
  }

  getLeaderboardPoints(): number {
    if (this.leaderboard == null) return 0
    return this.leaderboard.points
  }


  getAvatar(): string {
    let avatar = this.getAuth().avatar
    return avatar == null || avatar.trim().length === 0 ? 'assets/img/default-avatar.png' : avatar
  }

  onMenuClick(event: MouseEvent) {
    this.showMenubar = !this.showMenubar
  }

  getMenuItems(): IMenuItem[] {
    let items: IMenuItem[] = []

    items.push({
      id: 1,
      title: 'My Interests',
      code: 'MY_PROFILE'
    })

    items.push({
      id: 2,
      title: 'My Profile',
      code: 'MY_PROFILE'
    })

    if (this.isAuthenticated()) {
      items.push({
        id: 3,
        title: 'Logout',
        code: 'LOGOUT'
      })
    } else {
      items.push({
        id: 3,
        title: 'Login',
        code: 'LOGIN'
      })
    }
    return items
  }

  isAuthenticated(): boolean {
    return this.authService.isAuthenticated()
  }

  onMenuItemClick(item: IMenuItem) {
    switch (item.id) {
      case 2:
        this.navigate('/profile', value => {
        })
        break
      case 1:
        this.navigate('/choose-interests', value => {
        })
        break
      case 3:
        if (!this.isAuthenticated()) {
          this.navigate('/login', () => {
          })
          return
        }
        this.authService.logout()
        this.navigate('/', value => {
          this.notifier.warning('Logged out', 'You have been successfully logged out.', () => {
            this.router.navigate(['login']).then(r => {
            })
          }, 3000)
        })
        break
    }
  }

  openLeaderboard() {
    if (this.leaderboard == null) return
    const dialogRef = this.dialog.open(LeaderboardComponent, {
      width: '600px'
    });
    let instance = dialogRef.componentInstance
    instance.leaderboard = this.leaderboard
  }

  onWalletClick() {
    if (this.authenticated())
      this.openLeaderboard()
    else
      this.navigate('/login', value => {
      })
  }
}
