import {Component} from '@angular/core';
import {ICarouselItem} from "../../models/ICarouselItem";
import {BaseComponent} from "../../../../base/components/base.component";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";
import {AuthService} from "../../../common/modules/auth/services/auth.service";
import {ReviewService} from "../../services/review.service";
import {EventKey} from "../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {IReview} from "../../models/review/IReview";
import {NotifierService} from "../../../../services/notifier/notifier.service";
import {IPage} from "../../../../base/models/IPage";
import {IProduct} from "../../models/inventory/IProduct";
import {ProductService} from "../../services/product.service";
import {StringUtil} from "../../../../utils/string.util";
import {IRating} from "../../models/review/IRating";
import {MatDialog} from "@angular/material/dialog";
import {PostReviewComponent} from "../../components/reviews/post-review/post-review.component";
import {IAuth} from "../../../common/modules/auth/models/auth.model";
import {IMenuItem, menuItemsForPeriod} from "../../components/menubox/menu-item.model";
import {TimeUtility} from "../../../common/modules/utility/time.utility";
import {IParams} from "../../../../base/endpoints/iparams";
import {IReviewSlice} from "../../models/review/review-slice.model";
import {Meta, Title} from "@angular/platform-browser";

@Component({
  selector: 'app-product-details',
  templateUrl: './product-details.component.html',
  styleUrls: ['./product-details.component.scss'],
})
export class ProductDetailsComponent extends BaseComponent {
  carouselItems: ICarouselItem[] = []
  productId: number = 0
  productSlug: string = ''
  myReview: IReview | null = null
  avgRatings: IRating[] = []
  product: IProduct | null = null
  myReviewLoading = false

  criticReviews: IPage<IReviewSlice> | null = null
  criticReviewers: ICarouselItem[] = []
  criticReviewsPeriod = 'THIS MONTH'
  criticReviewsLoading = false
  showCriticReviewsMenu = false
  badgeForCritic: string = 'Critic'

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private auth: AuthService,
              private reviewService: ReviewService,
              private notifier: NotifierService,
              private productService: ProductService,
              private dialog: MatDialog,
              private seoTitle: Title,
              private seoMeta: Meta
  ) {
    super(eventBus, router, subContainer);
    this.productId = route.snapshot.params['id']
    this.productSlug = route.snapshot.params['slug']
  }

  protected componentName(): string {
    return "ProductDetailsComponent";
  }

  ngOnInit(): void {
    this.registerSubscriptions()
    this.fetchProductDetails()
  }

  private fetchProductDetails() {
    let key = this.componentKey(EventKey.PRODUCTS.PRODUCT_DETAILS_LOADED(this.productSlug))
    this.productService.fetchDetails(key, this.productSlug)
    this.subscribe(key, null, (value, meta) => {
      this.product = meta.data
      this.resolveSeo(this.product)
      this.fetchAvgRating(this.productId)

      if (this.auth.isAuthenticated()) {
        this.fetchMyReview(this.productId)
      }

      let cReviewsDateRange = TimeUtility.calculateRange(this.criticReviewsPeriod.replace(' ', '_'))
      this.fetchCriticReviews({
        page: 0,
        from_date: cReviewsDateRange?.fromDate,
        to_date: cReviewsDateRange?.toDate,
        slice: true,
        badge: this.badgeForCritic
      })
    })
  }

  private fetchMyReview(productId: number) {
    this.myReviewLoading = true
    let key = this.componentKey(EventKey.REVIEWS.LOAD_MY_REVIEW(productId))
    this.reviewService.fetchMyReview(key, this.productId)

    this.subscribe(key, null, (value: boolean, meta: MetaData) => {
      this.myReview = meta.data
      this.myReviewLoading = false
    })

    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.myReviewLoading = false
    })

  }

  private fetchAvgRating(productId: number) {
    let key = this.componentKey(EventKey.REVIEWS.LOADED_PRODUCT_AVG_RATINGS())
    this.productService.fetchAvgRatings(key, productId)

    this.subscribe(key, null, (value, meta) => {
      this.avgRatings = meta.data
    })
  }

  getMenuItems() {
    return menuItemsForPeriod
  }

  private fetchCriticReviews(params: IParams) {
    this.criticReviewsLoading = true
    let key = this.componentKey(EventKey.REVIEWS.FETCH_CRITIC_REVIEWS())
    this.reviewService.fetchProductReviews(key, this.productId, params)
    this.subscribe(key, null, (value, meta) => {
      this.criticReviews = meta.data
      this.criticReviewers = ProductDetailsComponent.mapToCarouselItems(meta.data)
      this.criticReviewsLoading = false
    })
  }

  private static mapToCarouselItems(recentReviews: IPage<IReviewSlice>): ICarouselItem[] {
    return recentReviews.content.map(review => {
      return {
        id: review?.id,
        slug: review?.slug,
        title: review?.posted_by_name,
        img: review?.posted_by_avatar != null ? review?.posted_by_avatar : 'assets/img/default-avatar.png',
        url: '/reviews/' + review.id + '/' + review.post_id,
        badges: []
      };
    });
  }

  onCriticReviewsMenuItemClick(item: IMenuItem) {
    if (this.showCriticReviewsMenu) this.showCriticReviewsMenu = false
    this.criticReviewsPeriod = item.title
    let dateRange = TimeUtility.calculateRange(item.code)
    this.fetchCriticReviews({
      page: 0,
      from_date: dateRange?.fromDate,
      to_date: dateRange?.toDate,
      slice: true,
      badge: this.badgeForCritic
    })
  }

  isLoggedIn(): boolean {
    return this.auth.isAuthenticated()
  }

  private registerSubscriptions() {
    let key = this.globalKey(EventKey.REVIEWS.MY_REVIEW_POSTED(this.productId))
    this.subscribe(key, null, (value, meta) => {
      this.myReview = meta.data
      this.myReviewLoading = false
    })
  }


  onUserItemClick(event: any) {
    this.notifier.log(event)
  }

  strSubstring(str: string | null | undefined, len: number) {
    let sub = StringUtil.takeOnly(str, len)
    if (str != null && str.length > len)
      sub = sub + '..See More..'
    return sub
  }

  getAuth(): IAuth {
    return this.auth.getAuth()
  }

  getAvatar(): string {
    let avatar = this.getAuth().avatar
    return avatar == null ? 'assets/img/default-avatar.png' : avatar
  }

  openPostReviewDialog() {
    if (!this.auth.isAuthenticated()) {
      this.router.navigate(['/login'])
      return
    }

    const dialogRef = this.dialog.open(PostReviewComponent, {disableClose: true});
    let instance = dialogRef.componentInstance
    instance.productId = this.productId

    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }

  private resolveSeo(product: IProduct | null) {
    if (product == null) return
    let title = 'Review - Rating for ' + product.name
    this.initSeoItems(
      this.seoTitle, this.seoMeta,
      title,
      product.description,
      ['Buy ' + product.name, ' review,' + product.name, ' rating, where to buy'],
      null,
      null,
      product?.cover_photo,
      this.getPermalink()
    )

    // this.seoTitle.setTitle('Review - Rating for ' + product.name)
    // this.seoMeta.addTags([
    //   {
    //     name: 'keywords',
    //     content:  + ',' + product.name + ' review,' + product.name + ' rating, where to buy'
    //   },
    //   {name: 'description', content: product.description},
    //   {name: 'robots', content: 'index, follow'},
    //   {property: 'og:type', content: 'article'},
    //   {property: 'og:title', content: 'article'},
    // ])
  }
}
