import {Component, Input} from '@angular/core';
import {BaseComponent} from "../../../../../base/components/base.component";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {PostService} from "../../../services/post.service";
import {EventKey} from "../../../../../services/eventbus/event.key";
import {ReactionTypes} from "../../../models/posts/reaction-types.enum";
import {IReactionSlice} from "../../../models/posts/reactions/reaction-slice.model";

@Component({
  selector: 'app-reaction-view',
  templateUrl: './reaction-view.component.html',
  styleUrls: ['./reaction-view.component.scss']
})
export class ReactionViewComponent extends BaseComponent {

  @Input() postId: number = 0
  reactions: IReactionSlice[] = []

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private postService: PostService
  ) {
    super(eventBus, router, subContainer);
  }

  protected componentName(): string {
    return "ReactionViewComponent";
  }

  ngOnInit(): void {
    this.fetchReactions(this.postId)
  }

  getReactionTypes(): string[] {
    let types = []
    for (let key in ReactionTypes) {
      types.push(key)
    }
    return types
  }

  filterReactions(type: string): IReactionSlice[] {
    return this.reactions.filter(r => r.reaction_type === type)
  }

  private fetchReactions(postId: number) {
    let key = this.componentKey(EventKey.POSTS.FETCH_POST_REACTIONS(postId))
    this.postService.fetchPostReactions(key, postId, {slice: true})
    this.subscribe(key, null, (value, meta) => {
      this.reactions = meta.data
    })
  }
}
