import {Component, Input, OnInit} from '@angular/core';
import {IUploadFile} from "../../../models/uploads/IUploadFile";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {BaseComponent} from "../../../../../base/components/base.component";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {EventKey} from "../../../../../services/eventbus/event.key";
import {IImageUploadResponse} from "../../../../../models/IImageUploadResponse";
import {FileUploadService} from "../../../../../services/files/file-upload.service";
import {IReviewVerificationReq} from "../../../models/review/review-verification-req.model";
import {ReviewService} from "../../../services/review.service";
import {HttpErrorResponse} from "@angular/common/http";

@Component({
  selector: 'app-verify-review',
  templateUrl: './verify-review.component.html',
  styleUrls: ['./verify-review.component.scss']
})
export class VerifyReviewComponent extends BaseComponent {
  @Input() reviewId: number | null = 0

  uploadFiles: IUploadFile[] = []

  verificationForm = new FormGroup({
    add_to_block_chain: new FormControl(''),
    comment: new FormControl('', [Validators.required, Validators.minLength(20)])
  })

  completionMsg: string | null = null

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private fileUploadService: FileUploadService,
    private notifier: NotifierService,
    private reviewService: ReviewService
  ) {
    super(eventBus, router, subContainer)
  }

  protected componentName(): string {
    return "VerifyReviewComponent";
  }

  ngOnInit(): void {
    this.loading = false
  }

  onImageSelected(event: any) {
    if (event.target.files && event.target.files[0]) {
      // validate image
      const mimeType = event.target.files[0].type;
      if (mimeType.match(/image\/*/) == null) {
        this.notifier.warning("Unsupported", "Only images are supported.");
        return;
      }

      let uf: IUploadFile = {
        file: event.target.files[0],
        previewUrl: null
      }

      const reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (e) => { // called once readAsDataURL is completed
        let url = e.target?.result
        if (url !== undefined && url !== null) {
          uf.previewUrl = url
          this.uploadFiles.push(uf);
        }
      }
    }
  }

  uploadImages(): void {
    this.loading = true

    let files: File[] = []
    this.uploadFiles.forEach(uf => {
      files.push(uf.file)
    });
    let key = this.componentKey(EventKey.FILES.IMAGE_UPLOAD(this.reviewId + '-verif-img'))
    this.fileUploadService.uploadImages(key, files)
    this.subscribe(
      key,
      null,
      (value, meta) => {
        let imageResponses = <IImageUploadResponse[]>meta.data
        let images = imageResponses.map(r => r.image_url)
        this.submitRequest(images)
      }
    );

  }

  removeImage(uf: IUploadFile) {
    let index = this.uploadFiles.map(file => file.previewUrl).indexOf(uf.previewUrl)
    this.uploadFiles.splice(index, 1)
  }

  submitVerificationRequest() {
    this.notifier.log(this.verificationForm)
    this.uploadImages()
  }

  private submitRequest(images: string[]) {
    if (this.reviewId == null) return
    this.loading = true

    let atb = this.verificationForm.controls['add_to_block_chain'].value
    let req: IReviewVerificationReq = {
      review_id: this.reviewId,
      add_to_block_chain: atb !== "",
      comment: this.verificationForm.controls['comment'].value,
      document_1: images.length > 0 ? images[0] : '',
      document_2: images.length > 1 ? images[1] : null,
      document_3: images.length > 2 ? images[2] : null
    }

    const key = this.componentKey(EventKey.REVIEWS.SEND_VERIFICATION_REQUEST(this.reviewId))
    this.reviewService.verifyReview(key, req)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false
      this.notifier.success('Success', "Request Sent.")
      this.completionMsg = 'Your request has been successfully submitted. You\'ll be notified once your request is reviewed.'
    })

    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.loading = false
      let error: HttpErrorResponse = meta.data
      this.completionMsg = error.error.message
      this.notifier.warning('Failed!', error.error.message)
    })

  }

  isInvalidFileCount(): boolean {
    return this.uploadFiles.length === 0
      || this.uploadFiles.length > 3
  }

  showInfo() {
    this.notifier.info(
      'Info!',
      'Upload related documents as a proof. For example invoice, payment slip etc.\n' +
      '      This will help us determine the authenticity of your affiliation with the item.',
() => {},
      10000
    )
  }
}
