import {Injectable} from '@angular/core';
import {environment} from "../../../environments/environment";
import {IParams} from "./iparams";

@Injectable({
  providedIn: 'root'
})
export class BaseEndpoint {

  constructor() {
  }

  static API_VERSION_1 = '/api/v1';
  static API_VERSION_2 = '/api/v2';

  static toParamString(params: IParams): string {
    let p: string = "";
    for (let key in params) {
      let value = params[key]
      if (value == null) continue
      if (p === '') p = p + '?'; else {
        p = p + '&';
      }
      p = p + key + '=' + this.convertToString(params[key])
    }
    return p
  }

  static convertToString(value: any) {
    if (value instanceof Date)
      return value.toISOString()
    return value.toString()
  }

  static FILES = {
    FILE_ENDPOINT: environment.base_url + BaseEndpoint.API_VERSION_2 + '/files',
    IMAGE_ENDPOINT: environment.base_url + BaseEndpoint.API_VERSION_2 + '/images',
    IMAGE_ENDPOINT_ADMIN: environment.base_url + BaseEndpoint.API_VERSION_2 + '/admin/images',
    IMAGE_UPLOAD_ENDPOINT_BULK: environment.base_url + BaseEndpoint.API_VERSION_2 + '/images/bulk',
    FILE_ENDPOINT_ADMIN: environment.base_url + BaseEndpoint.API_VERSION_2 + '/admin/files',
    SEARCH(endpoint: string, params: IParams) {
      return endpoint.concat(BaseEndpoint.toParamString(params));
    },
    CREATE(): string {
      return this.FILE_ENDPOINT
    },
    UPLOAD(params: IParams): string {
      return this.IMAGE_UPLOAD_ENDPOINT_BULK.concat(BaseEndpoint.toParamString(params));
    },
    GET(id: number): string {
      return this.FILE_ENDPOINT + '/' + id
    },
    UPDATE(id: number): string {
      return this.FILE_ENDPOINT + '/' + id
    },
  }

}


