import {Component} from '@angular/core';
import {Location} from "@angular/common";
import {AuthService} from "../../../common/modules/auth/services/auth.service";
import {BaseComponent} from "../../../../base/components/base.component";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";
import {NotifierService} from "../../../../services/notifier/notifier.service";
import {IAuth} from "../../../common/modules/auth/models/auth.model";
import {ILeaderboard} from "../../models/leaderboard/leaderboard.model";
import {MatDialog} from "@angular/material/dialog";
import {EventKey} from "../../../../services/eventbus/event.key";
import {IFollowStats} from "../../models/users/follow-stats.model";
import {UserService} from "../../services/user.service";
import {IFollow} from "../../models/users/follow.model";
import {FollowersComponent} from "../../components/followers/followers.component";
import {UpdateProfileComponent} from "../../components/update-profile/update-profile.component";
import {LeaderboardService} from "../../../common/modules/accounting/leaderboard/services/leaderboard.service";
import {WalletService} from "../../../common/modules/accounting/wallet/services/wallet.service";
import {LeaderboardComponent} from "../../../common/modules/accounting/leaderboard/leaderboard.component";

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent extends BaseComponent {

  leaderboard: ILeaderboard | null = null
  followStats: IFollowStats | null = null
  // wallet: IWallet | null = null
  username: string | null = null

  constructor(private location: Location,
              eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private authService: AuthService,
              private notifier: NotifierService,
              private leaderboardService: LeaderboardService,
              private walletService: WalletService,
              private dialog: MatDialog,
              private userService: UserService,
              route: ActivatedRoute) {
    super(eventBus, router, subContainer)
    this.username = route.snapshot.params['username']
  }

  protected componentName(): string {
    return "ProfileComponent";
  }

  ngOnInit(): void {
    if (this.username == null)
      this.username = this.getAuthUsername()

    this.fetchLeaderboard()
    this.fetchFollowStats()
  }

  getAuthUsername(): string {
    return this.getAuth().username
  }

  goBack(): void {
    this.location.back()
  }

  isLoadComplete() {
    return this.leaderboard != null
  }

  myProfile(): boolean {
    return this.username === this.getAuthUsername()
  }

  getAuth(): IAuth {
    return this.authService.getAuth()
  }

  private fetchLeaderboard() {
    if (this.username == null) return
    let key = this.componentKey(EventKey.LEADERBOARDS.FETCH_LEADERBOARD(this.username))
    this.leaderboardService.fetchLeaderboard(key, this.username)
    this.subscribe(key, null, (value, meta) => {
      this.leaderboard = meta.data
    })

  }

  getBadgeCount(): number {
    if (this.leaderboard == null) return 0
    return this.leaderboard.badges.length
  }

  private fetchFollowStats() {
    if (this.username == null) return
    let key = this.componentKey(EventKey.USERS.FETCH_FOLLOW_STATS(this.username))
    this.userService.fetchFollowStats(key, this.username)
    this.subscribe(key, null, (value, meta) => {
      this.followStats = meta.data
    })
  }

  follow(username: string | null) {
    if (username == null) return
    const key = this.componentKey(EventKey.USERS.FOLLOW_USER(username))
    this.userService.followUser(key, username, '')
    this.subscribe(key, null, (value, meta) => {
      let follow: IFollow = meta.data
      this.notifier.log(this.followStats)
      if (this.followStats)
        this.followStats.my_follow_state = follow.state
    })
  }

  unfollow(username: string | null) {
    if (username == null) return
    const key = this.componentKey(EventKey.USERS.UNFOLLOW_USER(username))
    this.userService.unfollowUser(key, username)
    this.subscribe(key, null, (value, meta) => {
      if (this.followStats)
        this.followStats.my_follow_state = null
    })
  }

  isMyProfile(): boolean {
    if (!this.authService.isAuthenticated()) return false
    return this.getAuthUsername() === this.username
  }

  openFollowingDialog(following: boolean) {
    if (!this.isMyProfile()) return
    const dialogRef = this.dialog.open(FollowersComponent);
    let instance = dialogRef.componentInstance
    instance.isFollowing = following

    instance.itemClick.subscribe((v: boolean) => {
      dialogRef.close()
    })

    dialogRef.afterClosed().subscribe(result => {
      this.reloadRoute()
    })
  }

  openLeaderboard() {
    if (this.leaderboard == null) return
    const dialogRef = this.dialog.open(LeaderboardComponent, {
      width: '600px',
    });
    let instance = dialogRef.componentInstance
    instance.leaderboard = this.leaderboard
    instance.username = this.username
    // instance.wallet = this.wallet
  }

  openUpdateProfileDialog() {
    if (!this.isMyProfile()) return
    const dialogRef = this.dialog.open(UpdateProfileComponent);
    let instance = dialogRef.componentInstance

    dialogRef.afterClosed().subscribe(result => {
      this.reloadRoute()
    })
  }
}
