import {Component} from '@angular/core';
import {BaseComponent} from "../../../../base/components/base.component";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";
import {ProductService} from "../../services/product.service";
import {FormControl} from "@angular/forms";
import {EventKey} from "../../../../services/eventbus/event.key";
import {IPage} from "../../../../base/models/IPage";
import {IProduct} from "../../models/inventory/IProduct";
import {IParams} from "../../../../base/endpoints/iparams";
import {IProductSlice} from "../../models/inventory/product-slice.model";

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss']
})
export class SearchComponent extends BaseComponent {

  query: FormControl = new FormControl('')

  productPage: IPage<IProduct> | null = null
  products: IProductSlice[] = []

  filters: IParams = {
    q: '',
    brand_id: null,
    category_id: null,
    min_retail_price: 0,
    max_retail_price: 90000
  }
  showFilter: boolean = false

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private productService: ProductService,
  ) {
    super(eventBus, router, subContainer)
  }

  protected componentName(): string {
    return "SearchComponent";
  }

  ngOnInit(): void {
    this.loading = false

    SearchComponent.setDefaultFocus()

  }

  onQueryEntering(event: Event) {
    if (this.query.value.length < 3) return
    this.filters['q'] = this.query.value
    this.searchProducts()
  }

  private static setDefaultFocus() {
    document.getElementById('search-input')?.focus()
  }


  private searchProducts() {
    this.loading = true
    let key = this.componentKey(EventKey.PRODUCTS.SEARCH_PRODUCTS())
    this.productService.search(key, this.filters)
    this.subscribe(key, null, (value, meta) => {
      let productPage: IPage<IProduct> = meta.data
      this.products = this.mapToSlice(productPage.content)
      this.loading = false
    })
  }

  onProductClick(p: IProductSlice) {
    this.navigate('/products/' + p.product_id + '/' + p.product_slug, value => {
      console.log(p)
    })
  }


  toggleFilter() {
    this.showFilter = !this.showFilter
  }

  onFilter(params: IParams) {
    params['q'] = this.query.value
    this.filters = params
    this.searchProducts()
  }

  private mapToSlice(products: IProduct[]) {
    let slices: IProductSlice[] = []
    products.forEach(p => {
      slices.push({
        image: p.cover_photo,
        description: p.description,
        product_id: p.id,
        product_name: p.name,
        product_slug: p.slug,
        average_rating: p.average_rating
      })
    })
    return slices
  }
}
