import {Injectable} from '@angular/core';
import {IAuth} from "../../modules/common/modules/auth/models/auth.model";
import {environment} from "../../../environments/environment";

@Injectable({
  providedIn: 'root'
})
export class LocalStorageService {

  constructor() {
  }

  public static KEYS: any = {
    clientId: '*&^%&%frgter^87i5',
    clientSecret: 'V%$#reg%786v*7i6',
    accessToken: 'B57UB5&ehyr&5b&*6',
    refreshToken: 'INB*gege&U4^345%36^5u',
    authorities: 'BUegeg&v3$76u%^&*',
    phone: 'phone',
    name: 'name',
    email: 'email',
    id: 'id',
    username: 'username',
    jti: 'Jjjsdb!*(&*@&#*&Y#',
  };

  static putAuth(user: IAuth) {
    localStorage.setItem(this.KEYS.id, user.id.toString());
    localStorage.setItem(this.KEYS.accessToken, user.access_token);
    localStorage.setItem(this.KEYS.refreshToken, user.refresh_token);
    localStorage.setItem(this.KEYS.phone, user.phone);
    localStorage.setItem(this.KEYS.name, user.name);
    localStorage.setItem(this.KEYS.email, user.email);
    localStorage.setItem(this.KEYS.username, user.username);
    localStorage.setItem(this.KEYS.jti, user.jti);
    // localStorage.setItem(this.KEYS.authorities, JSON.stringify(user.authorities));
  }

  static getAuth(): IAuth {
    const username = this.retrieve(this.KEYS.username) as string
    return {
      id: parseInt(this.retrieve(this.KEYS.id) as string),
      access_token: this.retrieve(this.KEYS.accessToken) as string,
      refresh_token: this.retrieve(this.KEYS.refreshToken) as string,
      name: this.retrieve(this.KEYS.name) as string,
      username: username,
      email: this.retrieve(this.KEYS.email) as string,
      phone: this.retrieve(this.KEYS.phone) as string,
      jti: this.retrieve(this.KEYS.jti) as string,
      avatar: environment.base_url + '/api/v1/public/users/' + username + '/avatar'
    }
  }

  static put(key: string, value: string) {
    localStorage.setItem(key, value);
  }

  static retrieve(key: string) {
    return localStorage.getItem(key);
  }

  static clear(): void {
    localStorage.clear();
  }


}
