import {IPageable} from "./IPageable";
import {ISort} from "./ISort";

export interface ResponseData<T> {
  type: string
  status: string
  code: number
  time: Date
  body: IPage<T>
}

export interface IPage<T> {
  content: T[];
  pageable: IPageable;
  totalPages: number;
  totalElements: number;
  last: boolean;
  first: boolean;
  sort: ISort;
  numberOfElements: number;
  size: number;
  number: number;
  empty: boolean;
}

export let defaultPage = {
  content: [],
  pageable: {
    sort: {
      sorted: false,
      unsorted: false,
      empty: false,
    },
    pageNumber: 0,
    pageSize: 10,
    offset: 0,
    paged: false,
    unpaged: false,
  },
  totalPages: 0,
  totalElements: 0,
  last: false,
  first: true,
  sort: {
    sorted: false,
    unsorted: false,
    empty: false,
  },
  numberOfElements: 0,
  size: 10,
  number: 0,
  empty: true,
}
