import {Component, EventEmitter, Input, Output} from '@angular/core';
import {IPost} from "../../../models/posts/IPost";
import {BaseComponent} from "../../../../../base/components/base.component";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {ReactionTypes} from "../../../models/posts/reaction-types.enum";
import {StringUtil} from "../../../../../utils/string.util";
import {PostTypes} from "../../../models/posts/post-types.enum";
import {PostService} from "../../../services/post.service";
import {EventKey} from "../../../../../services/eventbus/event.key";
import {MatDialog} from "@angular/material/dialog";
import {ReactionViewComponent} from "../reaction-view/reaction-view.component";
import {AuthService} from "../../../../common/modules/auth/services/auth.service";
import {PostBoxComponent} from "../post-box/post-box.component";
import {HttpErrorResponse, HttpResponse} from "@angular/common/http";
import {NotifierService} from "../../../../../services/notifier/notifier.service";

@Component({
    selector: 'app-post',
    templateUrl: './post.component.html',
    styleUrls: ['./post.component.scss']
})
export class PostComponent extends BaseComponent {
    @Input() isComment: boolean = false
    @Input() fullVersion: boolean = false
    @Input() post: IPost | null = null
    @Input() localMetaData: any | null
    @Input() highlighted: boolean = false

    showPostMenu: boolean = false
    replyToPostId: number = 0

    @Output() onDeletePost: EventEmitter<IPost> = new EventEmitter<IPost>()

    constructor(
        eventBus: EventBusService,
        router: Router,
        subContainer: SubscriptionContainerService,
        private postService: PostService,
        private dialog: MatDialog,
        private auth: AuthService,
        private notifier: NotifierService
    ) {
        super(eventBus, router, subContainer)
    }

    protected componentName(): string {
        return "PostComponent";
    }

    ngOnInit(): void {
    }

    onReacted(event: ReactionTypes) {
        this.reloadPost()
    }

    takePostThumbs(n: number): string[] {
        if (this.post == null || n < 1) return []
        if (this.post.thumbs.length < (n - 1)) return this.post.thumbs
        return this.post.thumbs.slice(0, n)
    }

    getFirstImage(): string {
        if (this.post == null || this.post.thumbs.length === 0)
            return ""
        return this.post.images[0]
    }

    getThumbLength(): number {
        if (this.post == null || this.post.thumbs == null) return 0
        return this.post.thumbs.length
    }

    getContent(): string {
        if (this.post == null) return ''
        if (this.fullVersion) return this.post.content
        return StringUtil.takeOnly(this.post.content, 200)
    }

    onReplyClick(postId: number | undefined) {
        if (postId == null) return
        if (this.replyToPostId === postId)
            this.replyToPostId = 0
        else
            this.replyToPostId = postId
    }

    getTotalComments(): number {
        if (this.post == null || this.post.reaction_stats == null) return 0
        return this.post.reaction_stats.comments
    }

    isReview(): boolean {
        return this.post?.type === PostTypes.USER_REVIEW
    }

    isReplyEnabled(): boolean {
        if (this.post == null) return false
        return this.post?.depth <= 1
    }

    private reloadPost() {
        if (this.post == null) return
        let key = this.componentKey(EventKey.POSTS.FETCH_POST(this.post?.id))
        this.postService.fetchPost(key, this.post.id)
        this.subscribe(key, null, (value, meta) => {
            this.post = meta.data
        })

    }

    onReactionCountClick(event: MouseEvent) {
        console.log('reactions clicked')
        const dialogRef = this.dialog.open(ReactionViewComponent);
        let instance = dialogRef.componentInstance
        if (this.post != null)
            instance.postId = this.post?.id

        dialogRef.afterClosed().subscribe(result => {
            console.log(`Dialog result: ${result}`);
        });
    }

    onItemClick(event: MouseEvent) {
        this.showPostMenu = !this.showPostMenu
    }

    isMyPost(): boolean {
        if (!this.auth.isAuthenticated()) return false
        return this.auth.getAuth().username == this.post?.posted_by_username
    }

    getAvgRating(): string | null {
        let averageRating = this.getMetaData(this.post, 'average_rating')
        if (averageRating == null) return null
        try {
            return parseFloat(averageRating).toFixed(1)
        } catch (e) {
            return null
        }
    }

    getProductName(): string | null {
        let name = this.getMetaData(this.post, 'product_name')
        return StringUtil.takeOnly(name, 20)
    }

    getMetaData(post: IPost | null, key: string): any {
        let metadata = post?.meta_data
        if (metadata == null) return null
        return metadata[key]
    }

    isShowMetaSection(): boolean {
        if (this.post?.meta_data == null) return false
        return this.post.type === PostTypes.USER_REVIEW
    }

    editPost(post: IPost | null | undefined) {
        if (post == null) return
        // this.onEditPost.emit(post)
        this.openPostBox(post)
    }

    openPostBox(post: IPost | null) {
        const dialogRef = this.dialog.open(PostBoxComponent, {disableClose: true});
        let instance = dialogRef.componentInstance
        instance.commentBox = false
        instance.post = post
        instance.parentId = post?.parent_id
        instance.onPosted.subscribe((post: IPost) => {
            this.reloadPost()
            dialogRef.close()
        })
        instance.onCancel.subscribe((close: boolean) => {
            dialogRef.close()
        })
    }

    openPostDetailsPage(post: IPost | null | undefined) {
        if (this.showPostMenu) {
            this.showPostMenu = false
            return;
        }
        if (post == null) return
        this.navigate('/posts/' + post.id, value => {
        })
    }

    deletePost(post: IPost | null | undefined) {
        if (post == null) return;
        let conf = confirm("Are you sure you want to delete this post?")
        if (!conf) return
        let key = this.componentKey(EventKey.POSTS.DELETE_POST(post.id))
        this.postService.deletePost(key, post.id)
        this.subscribe(key, null, (value, meta) => {
            this.notifier.success('Deleted!', 'Post is deleted.')
            this.reloadRoute()
        })
        this.subscribe(this.getErrorKey(key), null, (value, meta) => {
            let err: HttpErrorResponse = meta.data
            this.notifier.error('Error!', err.error.message)
        })
    }
}
