import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {ICategory} from "../../models/inventory/ICategory";
import {BaseComponent} from "../../../../base/components/base.component";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";
import {EventKey} from "../../../../services/eventbus/event.key";
import {CategoryService} from "../../services/category.service";
import {NotifierService} from "../../../../services/notifier/notifier.service";
import {IInterest} from "../../models/interests/iInterest";
import {FormControl} from "@angular/forms";
import {COMMA, ENTER} from "@angular/cdk/keycodes";
import {MatAutocompleteSelectedEvent} from "@angular/material/autocomplete";
import {MatChipInputEvent} from "@angular/material/chips";
import {Observable, of} from "rxjs";

@Component({
  selector: 'app-choose-interest',
  templateUrl: './choose-interest.component.html',
  styleUrls: ['./choose-interest.component.scss']
})
export class ChooseInterestComponent extends BaseComponent implements OnInit {
  categories: ICategory[] = []
  selectedCategories: ICategory[] = []

  searchInputCtl = new FormControl()
  separatorKeysCodes = [ENTER, COMMA];

  message: string = ''

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private categoryService: CategoryService,
              private notifier: NotifierService
  ) {
    super(eventBus, router, subContainer);
  }

  protected componentName(): string {
    return "ChooseInterestComponent";
  }

  ngOnInit(): void {
    this.loadCategories(null, null)
    this.loadMyInterests()
  }

  getAutoCompleteItems(): Observable<ICategory[]> {
    return of(this.categories)
  }

  loadCategories(parentId: number | null, query: string | null) {
    this.notifier.log('Loading child categories for: ' + parentId)
    let key = parentId == null ? EventKey.CATEGORIES.LOADED_ROOT_CATEGORIES() : EventKey.CATEGORIES.LOADED_CHILD_CATEGORIES()
    key = this.componentKey(key)
    this.categoryService.loadCategories(key, parentId, query)
    this.subscribe(key, null, (value, meta) => {
      this.categories = meta.data.content
      this.notifier.log(this.categories)
    })
  }

  chooseInterest(category: ICategory) {
    if (this.selectedCategories.map(value => value.id).indexOf(category.id) > -1) return
    this.selectedCategories.push(category)
  }

  removeInterest(category: ICategory) {
    let index = this.selectedCategories.map(value => value.id).indexOf(category.id)
    this.selectedCategories.splice(index, 1)
  }

  saveInterests() {
    let key = this.componentKey(EventKey.CATEGORIES.INTERESTS_SAVED())
    this.categoryService.saveInterests(key, this.selectedCategories)
    this.subscribe(key, "/", (value, meta) => {
      this.notifier.success('Success!', 'Interests Saved!')
      this.notifier.log(meta.data)
    })
    this.subscribe(this.getErrorKey(key), "/", (value, meta) => {
      this.notifier.error('Failed!', 'Could not save interests!')
      this.notifier.log(meta.data)
    })
  }

  private loadMyInterests() {
    let key = this.componentKey(EventKey.CATEGORIES.FETCH_INTERESTS())
    this.categoryService.fetchInterests(key)
    this.subscribe(key, null, (value, meta) => {
      let interests: IInterest[] = meta.data
      this.selectedCategories = interests.map(value => value.category)
      if (this.selectedCategories.length === 0) {
        this.selectedCategories = this.categories
        this.message = 'No interests found on your profile. So we automatically filled some items for you.' +
          ' You can remove, search for new items for interest profile.'
      }
    })
  }


  add(event: MatChipInputEvent): void {

  }

  selected(event: MatAutocompleteSelectedEvent): void {
    let selected: ICategory = event.option.value
    this.chooseInterest(selected)
    this.searchInputCtl.setValue('')
  }

  searchInputChange() {
    this.loadCategories(null, this.searchInputCtl.value)
  }
}
