import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {IMenuItem} from "./menu-item.model";

@Component({
  selector: 'app-menubox',
  templateUrl: './menubox.component.html',
  styleUrls: ['./menubox.component.scss']
})
export class MenuboxComponent implements OnInit {

  @Input() showMenubar: boolean = false
  @Input() top: number = 0
  @Input() items: IMenuItem[] = []
  @Output() menuItemClick: EventEmitter<IMenuItem> = new EventEmitter<IMenuItem>()

  constructor() {
  }

  ngOnInit(): void {
  }

  onMenuItemClick(item: IMenuItem) {
    this.menuItemClick.emit(item)
  }
}
