import {Component, OnInit} from '@angular/core';
import {BaseComponent} from "../../../../base/components/base.component";
import {Location} from "@angular/common";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";
import {AuthService} from "../../../common/modules/auth/services/auth.service";

@Component({
  selector: 'app-preference',
  templateUrl: './preference.component.html',
  styleUrls: ['./preference.component.scss']
})
export class PreferenceComponent extends BaseComponent {

  constructor(private location: Location,
              eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private authService: AuthService) {
    super( eventBus, router, subContainer)
  }

  protected componentName(): string {
    return "PreferenceComponent";
  }

  ngOnInit(): void {
  }

  onLogoutClick() {
    let logout = confirm("Are you sure you want to logout?")
    if (logout) {
      this.authService.logout()
      this.navigate('/login', value => {
      })
    }
  }
}
