import {Layout} from "../../../../../admin/components/breadcrumb/breadcrumb.model";
import {AdminEndpoint} from "../../../../../admin/admin.endpoint";
import {BaseConfig} from "../../../../../../base/models/baseConfig";


export interface FileConfig extends BaseConfig {
  imageEndpoint: string
  imgDelEndpoint: string
}

export let defaultFileConfig: FileConfig = {
  defaultLayout: Layout.GRID,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: true,
  searchEndpoint: AdminEndpoint.FILES.FILE_ENDPOINT,
  imageEndpoint: AdminEndpoint.FILES.IMAGE_ENDPOINT,
  delEndpoint: AdminEndpoint.FILES.FILE_ENDPOINT,
  imgDelEndpoint: AdminEndpoint.FILES.IMAGE_ENDPOINT
}


export let selfRoutes = {
  list: "/cms/files",
  create: "/cms/files/create",
  update(id: number | null) {
    return id == null ? this.create : "/admin/files/" + id + '/update'
  }
}
