import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {OwlOptions} from "ngx-owl-carousel-o";
import {IProduct} from "../../../models/inventory/IProduct";
import {StringUtil} from "../../../../../utils/string.util";
import {IProductSlice} from "../../../models/inventory/product-slice.model";

@Component({
  selector: 'app-product-carousel',
  templateUrl: './product-carousel.component.html',
  styleUrls: ['./product-carousel.component.scss']
})
export class ProductCarouselComponent implements OnInit {

  carouselOptions: OwlOptions = {
    loop: false,
    items: 6,
    smartSpeed: 500,
    autoplay: false,
    autoWidth: false,
    margin: 0,
    nav: false,
    dots: false,
    stagePadding: 10,
    responsive: {
      0: {
        items: 2,
        stagePadding: 10
      },
      480: {
        items: 2,
        stagePadding: 20
      },
      576: {
        items: 3
      },
      600: {
        items: 3
      },
      800: {
        items: 5
      },
      1200: {
        items: 6
      }
    }
  }
  @Input() items: IProductSlice[] = [];
  @Output() itemClick: EventEmitter<IProductSlice> = new EventEmitter<IProductSlice>();

  constructor() {
  }

  ngOnInit(): void {
  }

  onItemClick(item: IProductSlice) {
    this.itemClick.emit(item);
  }

  takeOnly(str: string): string {
    if (str.length < 25) return str
    return StringUtil.takeOnly(str, 25) + '..'
  }

}
