import {Component, Input} from '@angular/core';
import {BaseComponent} from "../../../../../base/components/base.component";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {EventKey} from "../../../../../services/eventbus/event.key";
import {PostService} from "../../../services/post.service";
import {IPage} from "../../../../../base/models/IPage";
import {IPost} from "../../../models/posts/IPost";

@Component({
  selector: 'app-post-list',
  templateUrl: './post-list.component.html',
  styleUrls: ['./post-list.component.scss']
})
export class PostListComponent extends BaseComponent {

  @Input() username: string | null = null

  posts: IPage<IPost> | null = null

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private postService: PostService
  ) {
    super(eventBus, router, subContainer)

  }

  protected componentName(): string {
    return "PostListComponent";
  }

  ngOnInit(): void {
    this.fetchPosts()
  }

  private fetchPosts() {
    if (this.username == null) return
    this.loading = true
    let key = this.componentKey(EventKey.POSTS.FETCH_USER_POSTS(this.username))
    this.postService.fetchUserPosts(key, this.username, {})
    this.subscribe(key, null, (value, meta) => {
      this.posts = meta.data
      this.loading = false
    })
  }
}
