import {Component, Input, OnInit} from '@angular/core';
import {IRating} from "../../../models/review/IRating";

@Component({
  selector: 'app-rating-view',
  templateUrl: './rating-view.component.html',
  styleUrls: ['./rating-view.component.scss']
})
export class RatingViewComponent implements OnInit {

  @Input() avgRating: number | null | undefined = 0.0
  @Input() ratings: IRating[] | undefined = []

  constructor() {
  }

  ngOnInit(): void {
  }

  getAvgRating(){
    return this.avgRating?.toFixed(1)
  }

  getRatings(): IRating[] {
    if (this.ratings == null) return []
    return this.ratings
  }

}
