import {Component, EventEmitter, Input, Output} from '@angular/core';
import {BaseComponent} from "../../../../../../base/components/base.component";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {LabelService} from "../../../../../admin/domains/labels-legacy/services/label.service";
import {ILabel} from "../../../../../admin/domains/labels-legacy/models/ilabel";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {WebAppEndpoint} from "../../../../../webapp/web-app.endpoint";
import {TextUtility} from "../../text.utility";
import {IParams} from "../../../../../../base/endpoints/iparams";

@Component({
  selector: 'app-label-filter',
  templateUrl: './label-filter.component.html',
  styleUrls: ['./label-filter.component.scss']
})
export class LabelFilterComponent extends BaseComponent {
  @Input() searchEndpoint: string = WebAppEndpoint.LABELS.SEARCH_ENDPOINT_ADMIN

  labels: ILabel[] = []
  @Input() selectedId: number | null = null
  @Output() selected: EventEmitter<number | null> = new EventEmitter<number | null>()

  protected override componentName(): string {
    return "LabelFilterComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private labelService: LabelService,) {
    super(eventBus, router, subContainer)
  }

  override ngOnInit(): void {
    this.loading = false
    this.searchLabels({
      page: 0,
      size: 10,
      q: null
    })
  }

  searchLabelsForEvent(event: any) {
    let query = TextUtility.removeSpecialCharacters(event.target?.value)
    if (query.length > 2) {
      this.searchLabels({
        page: 0,
        size: 10,
        q: query
      })
    }
  }

  onSelected(event: any) {
    this.selected.emit(this.selectedId)
  }

  searchLabels(params: IParams) {
    this.loading = true
    let key = this.componentKey(EventKey.LABELS_LEGACY.SEARCH())
    this.labelService.searchLabels(key, this.searchEndpoint, params)
    this.subscribe(key, null, (value, meta) => {
      this.labels = meta.data.content
      this.loading = false
    })
  }
}
