import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {HomeComponent} from "./pages/home/home.component";
import {ProductDetailsComponent} from "./pages/product-details/product-details.component";
import {NewsfeedComponent} from "./pages/newsfeed/newsfeed.component";
import {ChooseInterestComponent} from "./pages/choose-interest/choose-interest.component";
import {ProfileComponent} from "./pages/profile/profile.component";
import {AuthGuardService} from "../common/modules/auth/services/auth-guard.service";
import {ReviewDetailsComponent} from "./pages/review-details/review-details.component";
import {PostDetailsComponent} from "./pages/post-details/post-details.component";
import {SearchComponent} from "./pages/search/search.component";
import {MyNotificationsComponent} from "./pages/my-notifications/my-notifications.component";

const routes: Routes = [
  {path: '', component: HomeComponent},
  {path: 'products/:id/:slug', component: ProductDetailsComponent},
  {path: 'newsfeed', component: NewsfeedComponent, canActivate: [AuthGuardService]},
  {path: 'choose-interests', component: ChooseInterestComponent, canActivate: [AuthGuardService]},
  {path: 'profile', component: ProfileComponent, canActivate: [AuthGuardService]},
  {path: 'u/:username', component: ProfileComponent, canActivate: [AuthGuardService]},
  {
    path: 'reviews/:productSlug/:reviewSlug/:postId',
    component: ReviewDetailsComponent,
    canActivate: [AuthGuardService]
  },
  {path: 'posts/:postId', component: PostDetailsComponent, canActivate: [AuthGuardService]},
  {path: 'search', component: SearchComponent},
  {path: 'notifications', component: MyNotificationsComponent, canActivate: [AuthGuardService]}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class WebAppRoute {
}
