import {Component, ElementRef, Renderer2, ViewChild} from '@angular/core';
import {BaseComponent} from "../../../../base/components/base.component";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router, UrlTree} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";
import {AuthService} from "../../../common/modules/auth/services/auth.service";
import {IAuth} from "../../../common/modules/auth/models/auth.model";
import {DialogService} from "../../../common/modules/utility/components/popup/dialog.service";
import {LeaderboardComponent} from "../../../common/modules/accounting/leaderboard/leaderboard.component";
import {FormControl} from "@angular/forms";
import {translations} from "../left-nav/menu.model";

@Component({
  selector: 'app-admin-header',
  templateUrl: './admin-header.component.html',
  styleUrls: ['./admin-header.component.scss']
})
export class AdminHeaderComponent extends BaseComponent {
  @ViewChild('rightBarToggle') rightBarToggle: ElementRef;

  search = new FormControl( )

  auth: IAuth
  isAdmin: boolean = false

  authorities: string[] = []

  protected componentName(): string {
    return "AdminHeaderComponent";
  }



  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private authService: AuthService,
    private renderer: Renderer2,
    private popupService: DialogService<null>,
    private activatedRoute: ActivatedRoute,
  ) {
    super(eventBus, router, subContainer)
    this.auth = this.authService.getAuth()
    this.isAdmin = this.authService.isAdmin()
    this.authorities = authService.getAuthorities()
  }

  hasPrivilege(privilege: string): boolean {
    return this.isAdmin || this.authorities.includes(privilege)
  }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      this.search.setValue(params['query'])
    })
  }

  onSearch() {
    const urlTree: UrlTree = this.router.parseUrl(this.router.url);
    const path = urlTree.root.children['primary'].segments.map(segment => segment.path).join('/');

    this.router.navigate([path], {
      queryParams: {
        query: this.search.value,
      },
      queryParamsHandling: 'merge',
    }).then(r => {
    });
  }

  clearSearch() {
    this.search.setValue(null)
    this.onSearch()
  }

  logout() {
    this.authService.logout();
  }

  initLeftMenuCollapse() {
    const body = document.body;

    body.classList.toggle('sidebar-enable');
    if (window.innerWidth >= 992) {
      body.classList.toggle('vertical-collpsed');
    } else {
      body.classList.remove('vertical-collpsed');
    }

    if (body.classList.contains('vertical-collpsed')) {
      this.renderer.addClass(body, 'vertical-collpsed');
    } else {
      this.renderer.removeClass(body, 'vertical-collpsed');
    }
  }

  initRightSidebar() {
    // right side-bar toggle
    const body = document.querySelector('body');

    this.renderer.listen(body, 'click', (e: Event) => {
      const target = e.target as HTMLElement;

      if (target.closest('.right-bar-toggle, .right-bar')) {
        return;
      }

      this.renderer.removeClass(body, 'right-bar-enabled');
    });

    this.renderer.listen(document.querySelector('.right-bar-toggle'), 'click', () => {
      this.renderer.addClass(body, 'right-bar-enabled');
    });
  }

  openLeaderboard() {
    let popup = this.popupService.open(LeaderboardComponent, {id: null})
    let component = popup.componentInstance
    component.onCanceled.subscribe(() => {
      popup.close()
    })
  }

}
