import {Injectable} from '@angular/core';
import {AuthService} from "../../modules/common/modules/auth/services/auth.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {BaseEndpoint} from "../../base/endpoints/base.endpoint";
import {IImageUploadResponse} from "../../models/IImageUploadResponse";
import {BaseService} from "../../base/services/base.service";
import {NotifierService} from "../notifier/notifier.service";
import {EventBusService} from "../eventbus/event-bus.service";
import {EventKey} from "../eventbus/event.key";

@Injectable({
  providedIn: 'root'
})
export class FileUploadService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBusService: EventBusService
  ) {
    super(authService, notifier);
  }

  upload(files: FileList) {
    const formData = new FormData();
    for (let i = 0; i < files.length; i++) {
      formData.append('files', files[i]);
    }
    let url = BaseEndpoint.FILES.UPLOAD({
      'namespace': 'files',
      'access_token': this.authService.getAccessToken()
    });
    return this.http.post(url, formData);
  }

  uploadImages(eventKey: string, files: File[]) {
    const formData = new FormData();
    for (let i = 0; i < files.length; i++) {
      formData.append('files', files[i]);
    }
    let url = BaseEndpoint.FILES.UPLOAD({
      'namespace': 'files',
      'access_token': this.authService.getAccessToken()
    });
    this.http.post<IImageUploadResponse[]>(url, formData)
      .subscribe((response: IImageUploadResponse[]) => {
        this.eventBusService.publish(eventKey, response);
      }, (error: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(eventKey), error)
      });
  }


}
