import {Component, OnInit} from '@angular/core';
import {Step} from "../../models/step.enum";
import {AbstractControl, FormControl, Validators} from "@angular/forms";
import {BaseComponent} from "../../../../../../base/components/base.component";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {AuthService} from "../../services/auth.service";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {IResetPassword} from "../../models/reset-password.model";
import {ValidationUtil} from "../../../../../../utils/validation.util";
import {HttpErrorResponse} from "@angular/common/http";

@Component({
    selector: 'app-reset-password',
    templateUrl: './reset-password.component.html',
    styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent extends BaseComponent {
    step: Step = Step.INIT_VERIFICATION
    token = new FormControl('', [Validators.required, Validators.minLength(5)]);
    username = new FormControl(
        '',
        [
            Validators.required,
            Validators.minLength(6),
            Validators.maxLength(11)
        ]
    );
    password = new FormControl('', [Validators.required, Validators.minLength(6)]);

    tokenValidity: Date = new Date()

    constructor(
        eventBus: EventBusService,
        router: Router,
        subContainer: SubscriptionContainerService,
        private notifier: NotifierService,
        private authService: AuthService
    ) {
        super(eventBus, router, subContainer);
    }

    protected componentName(): string {
        return "ResetPasswordComponent";
    }

    ngOnInit(): void {
    }

    isVerified(): boolean {
        return this.step === Step.VERIFIED
    }

    isInitiating(): boolean {
        return this.step === Step.INIT_VERIFICATION
    }

    isWaitingForCode(): boolean {
        return this.step === Step.WAITING_FOR_CODE
    }

    requestToken() {
        if (this.username.invalid) {
            this.notifier.warning("Warning", "Username must be at least 6 characters!")
            return
        }
        let u = this.username.value
        this.notifier.log(u)
        let key = this.componentKey(EventKey.RESET_PASSWORD.TOKEN_REQUESTED)
        this.authService.requestPasswordResetToken(key, u!!)

        this.subscribe(key, null, (value, meta) => {
            this.step = Step.WAITING_FOR_CODE
            let rp: IResetPassword = meta.data
            this.tokenValidity = new Date(new Date().getMilliseconds() + rp.tokenValidityMillis)
        })
        this.subscribe(this.getErrorKey(key), null, (value, meta) => {
            let error: HttpErrorResponse = meta.data
            this.notifier.warning('Failed!', error.error.message)
        })
        // this.registrationForm.controls['username'].setValue(this.phone.value)
        this.notifier.log(u)
    }

    verifyOtp() {
        this.step = Step.VERIFIED
    }

    resetPassword() {
        let key = this.componentKey(EventKey.RESET_PASSWORD.RESET_SUCCESS)
        this.authService.resetPassword(
            key,
            this.token.value!!,
            this.username.value!!,
            this.password.value!!
        )
        this.subscribe(key, '/login', (value, meta) => {
            this.notifier.success('Success!', 'Password has been reset successfully. Please login to continue.')
        })
        this.subscribe(this.getErrorKey(key), null, (value, meta) => {
            let error: HttpErrorResponse = meta.data
            this.notifier.warning('Failed!', error.error.message)
        })
    }

    isVerifyButtonDisabled() {
        return this.username.invalid || this.token.invalid || this.password.invalid
    }
}
