import {Component, Input} from '@angular/core';
import {IReview} from "../../../models/review/IReview";
import {ReactionTypes} from "../../../models/posts/reaction-types.enum";
import {BaseComponent} from "../../../../../base/components/base.component";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {ReviewService} from "../../../services/review.service";
import {EventKey} from "../../../../../services/eventbus/event.key";
import {ReactionViewComponent} from "../../social/reaction-view/reaction-view.component";
import {MatDialog} from "@angular/material/dialog";
import {PostReviewComponent} from "../post-review/post-review.component";
import {AuthService} from "../../../../common/modules/auth/services/auth.service";
import {VerifyReviewComponent} from "../verify-review/verify-review.component";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {NoticeViewComponent} from "../../notice-view/notice-view.component";
import {VerificationDetailsComponent} from "../verification-details/verification-details.component";
import {StringUtil} from "../../../../../utils/string.util";
import {TextUtility} from "../../../../common/modules/utility/text.utility";
import {PostReportComponent} from "../../reports/post-report/post-report.component";
import {ReportDetailsComponent} from "../../reports/report-details/report-details.component";
import {PostReportService} from "../../../services/post-report.service";

@Component({
  selector: 'app-single-review-item',
  templateUrl: './single-review-item.component.html',
  styleUrls: ['./single-review-item.component.scss']
})
export class SingleReviewItemComponent extends BaseComponent {

  @Input() review: IReview | null = null
  @Input() tinted: boolean = false
  showMenuBox: boolean = false

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private reviewService: ReviewService,
    private dialog: MatDialog,
    private auth: AuthService,
    private notifier: NotifierService,
    private reportService: PostReportService
  ) {
    super(eventBus, router, subContainer)
  }

  protected componentName(): string {
    return "SingleReviewItemComponent";
  }

  ngOnInit(): void {
  }

  onReacted(event: ReactionTypes) {
    this.reloadReview()
  }

  private reloadReview() {
    if (this.review == null || this.review.id == null) return
    let key = this.componentKey(EventKey.REVIEWS.LOAD_REVIEW(this.review.slug))
    this.reviewService.fetchReview(key, this.review?.product_slug, this.review?.slug)
    this.subscribe(key, null, (value, meta) => {
      this.review = meta.data
    })
  }

  takeReviewThumbs(n: number): string[] {
    if (this.review == null || n < 1) return []
    if (this.review.thumbs.length < (n - 1)) return this.review.thumbs
    return this.review.thumbs.slice(0, n)
  }

  onReactionCountClick(event: MouseEvent) {
    console.log('reactions clicked')
    const dialogRef = this.dialog.open(ReactionViewComponent);
    let instance = dialogRef.componentInstance
    if (this.review?.post != null)
      instance.postId = this.review.post?.id

    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });

  }

  onItemClick(event: MouseEvent) {
    this.showMenuBox = !this.showMenuBox
  }

  openReviewDetails() {
    if (this.showMenuBox) {
      this.showMenuBox = false
      return
    }
    this.navigate(
      '/reviews/' + this.review?.product_slug + "/" + this.review?.slug + '/' + this.review?.post?.id,
      value => {
      })

  }

  onEditReviewClick() {
    if (this.review == null) return

    if (this.review.verification_status === 'VERIFIED') {
      // warn user that they are going to edit a verified review, it'll cause it to unverify.
      this.showVerifiedReviewEditWarning()
    } else
      this.openEditReviewDialog()

  }

  private showVerifiedReviewEditWarning() {
    const dialogRef = this.dialog.open(NoticeViewComponent);
    let instance = dialogRef.componentInstance
    instance.title = 'YOU\'RE EDITING A VERIFIED REVIEW!!'
    instance.message = `
    If you update a verified review, it'll cause this review to be unverified automatically.
       In this case you'll need to apply for verification again.
       `
    instance.positiveBtnText = 'EDIT'
    instance.negativeBtnTxt = 'CANCEL'
    instance.onPositiveBtn.subscribe(() => {
      this.openEditReviewDialog()
      dialogRef.close()
    })
    instance.onNegativeBtn.subscribe(() => {
      dialogRef.close()
    })
    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }

  openEditReviewDialog() {
    if (this.review == null) return
    const dialogRef = this.dialog.open(PostReviewComponent);
    let instance = dialogRef.componentInstance
    instance.review = this.review
    instance.productId = this.review.product_id

    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }

  isMine(): boolean {
    if (!this.auth.isAuthenticated()) return false
    const auth = this.auth.getAuth()
    return this.review?.post?.posted_by_username === auth.username
  }

  canRequestVerification(): boolean {
    return this.isMine() && this.review?.verification_status == null
  }

  hasVerification() {
    return this.isMine() && this.review?.verification_status != null
  }

  isVerified(): boolean {
    return this.review?.verification_status === 'VERIFIED'
  }

  openVerificationDialog() {
    if (this.review == null) return
    if (this.review.verification_status != null) {
      this.openVerificationDetails()
      return;
    }

    const dialogRef = this.dialog.open(VerifyReviewComponent)
    const instance = dialogRef.componentInstance
    instance.reviewId = this.review.id

    dialogRef.afterClosed().subscribe(result => {
      this.reloadReview()
    });
  }

  resolveReportView() {
    if (this.review?.post == null) return
    let key = this.componentKey(EventKey.REVIEWS.GET_REPORT_EXISTS(this.review?.post.id))
    this.reportService.getReportCountForPost(key, this.review.post.id)
    this.subscribe(key, null, (value, meta) => {
      let count = meta.data
      if (count > 0)
        this.openReportDetails()
      else this.openReportDialog()
      this.loading = false
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
      this.loading = false
    })
  }

  openReportDialog() {
    if (this.review?.post == null) return

    const dialogRef = this.dialog.open(PostReportComponent)
    const instance = dialogRef.componentInstance
    instance.reviewId = this.review.id
    instance.postId = this.review.post?.id

    dialogRef.afterClosed().subscribe(result => {
      this.reloadReview()
    });
  }

  onVerifiedMarkClick() {
    this.notifier.success("Verified Review", "This review has been verified by " + this.review?.verified_by)
  }


  openVerificationDetails() {
    if (this.review == null) return
    const dialogRef = this.dialog.open(VerificationDetailsComponent)
    const instance = dialogRef.componentInstance
    instance.reviewId = this.review.id

    dialogRef.afterClosed().subscribe(result => {
    });
  }

  openReportDetails() {
    if (this.review?.post == null) return
    const dialogRef = this.dialog.open(ReportDetailsComponent)
    const instance = dialogRef.componentInstance
    instance.reviewId = this.review.id
    instance.postId = this.review.post.id

    dialogRef.afterClosed().subscribe(result => {
    });
  }

  isBlockChained() {
    if (this.review == null) return false
    return this.review.chained
  }

  onBlockChainMarkClick() {
    if (this.review == null) return
    let hash: string = this.review.hash
    this.notifier.info(
      "Info",
      "Blockchain Digest: " + hash,
      () => {
        TextUtility.copyToClipboard(hash)
      },
      5000
    )
  }
}
