import {Component, OnInit} from '@angular/core';
import {IPage} from "../../../../base/models/IPage";
import {BaseComponent} from "../../../../base/components/base.component";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";
import {IParams} from "../../../../base/endpoints/iparams";
import {EventKey} from "../../../../services/eventbus/event.key";
import {INotification} from "../../../common/modules/social/notifications/models/notification.model";
import {NotificationService} from "../../../common/modules/social/notifications/services/notification.service";

@Component({
  selector: 'app-my-notifications',
  templateUrl: './my-notifications.component.html',
  styleUrls: ['./my-notifications.component.scss']
})
export class MyNotificationsComponent extends BaseComponent {

  notifications: IPage<INotification> | null = null
  params: IParams = {
    page: 0
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private notificationService: NotificationService
  ) {
    super(eventBus, router, subContainer)
  }

  protected componentName(): string {
    return "MyNotificationsComponent";
  }

  ngOnInit(): void {
    this.fetchNotifications(this.params)
  }

  private fetchNotifications(params: IParams) {
    this.loading = true
    let key = this.componentKey(EventKey.NOTIFICATIONS.FETCH_MY_NOTIFICATIONS())
    this.notificationService.fetchMyNotifications(key, params)
    this.subscribe(key, null, (value, meta) => {
      this.notifications = meta.data
      this.loading = false
    })
  }

  onNotificationItemClick(notification: INotification) {
    if (notification.redirect_url !== "") {
      this.router.navigateByUrl(notification.redirect_url)
    }
  }

  onMarkRead(actionRead: { notificationId: number, read: boolean }) {
    let key = this.componentKey(EventKey.NOTIFICATIONS.MARK_READ())
    let countKey = this.globalKey(EventKey.NOTIFICATIONS.COUNT_UNREAD())
    this.notificationService.markRead(key, countKey, actionRead.notificationId, actionRead.read)
    this.subscribe(key, null, (value, meta) => {
      this.fetchNotifications(this.params)
    })
  }
}
