import {Component, OnInit} from '@angular/core';
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {AuthService} from "../../services/auth.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {Router} from "@angular/router";
import {BaseComponent} from "../../../../../../base/components/base.component";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent extends BaseComponent implements OnInit {

  loginForm = new FormGroup({
    username: new FormControl('', Validators.required),
    password: new FormControl('', Validators.required),
  });

  constructor(private authService: AuthService,
              private notifier: NotifierService,
              eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService) {
    super(eventBus, router, subContainer)
  }

  protected componentName(): string {
    return "LoginComponent";
  }

  ngOnInit(): void {
    if (this.authService.isAuthenticated())
      this.navigateByUrl("/", r => {
      })
  }


  login(): void {
    let key = this.componentKey(EventKey.AUTH.LOGIN_SUCCESS)
    this.authService.login(key, this.loginForm.value.username!!, this.loginForm.value.password!!);
    this.subscribe(key, null, value => {
      this.navigate("/", route => {
        this.notifier.success("Success!", 'Successfully logged in.');
      })
    })
    this.subscribe(this.getMultiErrorKey(key, 1), '/auth/login', value => {
      this.notifier.error("Something went wrong!", 'Error happened on server.');
    });
    this.subscribe(this.getMultiErrorKey(key, 2), '/auth/login', value => {
      this.notifier.warning("Login Failed!", 'Wrong credentials or missing access rights to application.');
    });
  }

  isAuthenticated(): boolean {
    return this.authService.isAuthenticated();
  }

  forgotPassword() {
    this.notifier.info("Later!", "Will be implemented later!")
  }

  register() {
    this.notifier.info("Later!", "Will be implemented later!")
  }


}
