import {Component} from '@angular/core';
import {ICarouselItem} from "../../models/ICarouselItem";
import {BaseComponent} from "../../../../base/components/base.component";
import {AuthService} from "../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../services/notifier/notifier.service";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";
import {CategoryService} from "../../services/category.service";
import {EventKey} from "../../../../services/eventbus/event.key";
import {IInterest} from "../../models/interests/iInterest";
import {ICategory} from "../../models/inventory/ICategory";
import {IAttribute} from "../../models/review/iAttribute";
import {ReviewService} from "../../services/review.service";
import {IPage} from "../../../../base/models/IPage";
import {IMenuItem, menuItemsForPeriod} from "../../components/menubox/menu-item.model";
import {TimeUtility} from "../../../common/modules/utility/time.utility";
import {IParams} from "../../../../base/endpoints/iparams";
import {IReviewSlice} from "../../models/review/review-slice.model";
import {IAchievement} from "../../models/leaderboard/achievement.model";
import {LeaderboardService} from "../../../common/modules/accounting/leaderboard/services/leaderboard.service";
import {environment} from "../../../../../environments/environment";
import {Meta, Title} from "@angular/platform-browser";

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent extends BaseComponent {
  interestsAttrLoading: boolean = true

  recentReviews: IPage<IReviewSlice> | null = null
  recentReviewers: ICarouselItem[] = []
  reviewersLoading = true
  reviewersPeriod: string = 'THIS WEEK'
  showReviewersMenu: boolean = false

  recentAchievements: IPage<IAchievement> | null = null
  achievements: ICarouselItem[] = []
  achievementsLoading = true
  achievementsPeriod: string = 'THIS WEEK'
  showAchievementsMenu: boolean = false

  userInterests: ICategory[] = []
  attributes: IAttribute[] = []
  selectedInterestItem: number | null = null


  constructor(private authService: AuthService,
              private notifier: NotifierService,
              eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private categoryService: CategoryService,
              private reviewService: ReviewService,
              private leaderboardService: LeaderboardService,
              private seoTitle: Title,
              private seoMeta: Meta) {
    super(eventBus, router, subContainer)
  }

  protected componentName(): string {
    return "HomeComponent";
  }


  ngOnInit(): void {
    /*
    TEMPORARY REDIRECT
     */
    this.router.navigate(['/admin/dashboard']).then(r => {})

    this.fetchInterests()
    let dateRange = TimeUtility.calculateRange(this.reviewersPeriod.replace(' ', '_'))
    this.fetchRecentReviews({
      page: 0,
      from_date: dateRange?.fromDate,
      to_date: dateRange?.toDate,
      slice: true
    });
    this.fetchAchievements({
      from_date: dateRange?.fromDate,
      to_date: dateRange?.toDate
    });

    this.resolveSeo()
  }

  private fetchRecentReviews(params: IParams) {
    this.reviewersLoading = true
    let key = this.componentKey(EventKey.REVIEWS.FETCH_RECENT_REVIEWS())
    this.reviewService.fetchRecentReviews(key, params)
    this.subscribe(key, null, (value, meta) => {
      this.recentReviews = meta.data
      this.recentReviewers = HomeComponent.mapToCarouselItems(meta.data)
      this.reviewersLoading = false
    })
  }

  private fetchAchievements(params: IParams) {
    this.achievementsLoading = true
    let key = this.componentKey(EventKey.LEADERBOARDS.FETCH_RECENT_ACHIEVEMENTS())
    this.leaderboardService.fetchRecentAchievements(key, params)
    this.subscribe(key, null, (value, meta) => {
      this.recentAchievements = meta.data
      this.achievements = HomeComponent.mapAchievementsToCarouselItems(meta.data)
      this.achievementsLoading = false
    })
  }

  onRecentReviewerItemClick(item: ICarouselItem) {
    let reviews = this.recentReviews?.content.filter(r => r.id === item.id)
    if (reviews == null || reviews.length === 0) return
    let review = reviews[0]
    this.navigate('/reviews/' + review?.product_slug + "/" + review?.slug + '/' + review?.post_id, value => {

    })
  }

  onRecentAchievementsItemClick(item: ICarouselItem) {
    let achievements = this.recentAchievements?.content.filter(r => r.id === item.id)
    if (achievements == null || achievements.length === 0) return
    let achievement = achievements[0]
    this.navigate('/u/' + achievement.achieved_by_username, value => {

    })
  }

  private fetchInterests() {
    if (!this.authService.isAuthenticated()) {
      this.fetchRootCategories()
      return
    }
    let key = this.componentKey(EventKey.CATEGORIES.FETCH_INTERESTS())
    this.categoryService.fetchInterests(key)
    this.subscribe(key, null, (value, meta) => {
      let interests: IInterest[] = meta.data
      this.userInterests = interests.map(interest => interest.category)
      this.loadAttributes(this.userInterests)
    })
  }


  fetchRootCategories() {
    let key = this.componentKey(EventKey.CATEGORIES.LOADED_ROOT_CATEGORIES())
    this.categoryService.loadCategories(key, null, null)
    this.subscribe(key, null, (value, meta) => {
      this.userInterests = meta.data.content
      this.loadAttributes(this.userInterests)
    })
  }

  loadAttributes(categories: ICategory[]) {
    // set selected interest item
    this.selectedInterestItem = categories.length == 1 ? categories[0].id : null

    let categoryIds = categories.map(value => value.id)
    let key = this.componentKey(EventKey.CATEGORIES.LOADED_CATEGORY_ATTRIBUTES(categoryIds))
    this.categoryService.loadAttributes(key, categoryIds)
    this.subscribe(key, null, (value, meta) => {
      this.attributes = meta.data
      this.interestsAttrLoading = false
    })

    this.notifier.log('Initiate attributes for interests (unimplemented)')
  }

  onSearchBoxClicked(event: MouseEvent) {
    this.navigate('/search', value => {
    })
  }

  private static mapToCarouselItems(recentReviews: IPage<IReviewSlice>): ICarouselItem[] {
    return recentReviews.content.map(review => {
      return {
        id: review?.id,
        slug: review?.slug,
        title: review?.posted_by_name,
        img: review?.posted_by_avatar != null ? review?.posted_by_avatar : 'assets/img/default-avatar.png',
        url: '/reviews/' + review.id + '/' + review.post_id,
        badges: []
      };
    });
  }

  private static mapAchievementsToCarouselItems(achievements: IPage<IAchievement>): ICarouselItem[] {
    return achievements.content.map(a => {
      return {
        id: a?.id,
        slug: '',
        title: a?.badge_name,
        img: a?.achieved_by_avatar != null ? a?.achieved_by_avatar : 'assets/img/default-avatar.png',
        url: '/u/' + a.achieved_by_username,
        badges: Array(a?.total_badge_count).fill("assets/img/recent-acchievements/rating/badge1.png")
      };
    });
  }

  getMenuItems(): IMenuItem[] {
    return menuItemsForPeriod
  }

  onReviewersMenuItemClick(item: IMenuItem) {
    if (this.showReviewersMenu) this.showReviewersMenu = false
    this.reviewersPeriod = item.title
    let dateRange = TimeUtility.calculateRange(item.code)
    this.fetchRecentReviews({
      page: 0,
      from_date: dateRange?.fromDate,
      to_date: dateRange?.toDate,
      slice: true
    })
  }

  onAchievementsMenuItemClick(item: IMenuItem) {
    console.log(item)
    if (this.showAchievementsMenu) this.showAchievementsMenu = false
    this.achievementsPeriod = item.title
    let dateRange = TimeUtility.calculateRange(item.code)
    console.log(dateRange?.fromDate)
    console.log(dateRange?.toDate)
    this.fetchAchievements({
      from_date: dateRange?.fromDate,
      to_date: dateRange?.toDate
    })
  }

  onReviewersMenuClick() {
    this.showReviewersMenu = !this.showReviewersMenu
  }

  onAchievementsMenuClick() {
    this.showAchievementsMenu = !this.showAchievementsMenu
  }

  private resolveSeo() {
    this.initSeoItems(
      this.seoTitle,
      this.seoMeta,
      environment.app_name,
      'Blockchain based community oriented product quality assurance platform through review and rating.',
      [],
      'index',
      'website',
      '',
      environment.app_url
    )
  }
}
