import {Component, Input, OnInit} from '@angular/core';
import {IReviewVerification} from "../../../models/review/review-verification.model";
import {BaseComponent} from "../../../../../base/components/base.component";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {ReviewService} from "../../../services/review.service";
import {AuthService} from "../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {EventKey} from "../../../../../services/eventbus/event.key";

@Component({
  selector: 'app-verification-details',
  templateUrl: './verification-details.component.html',
  styleUrls: ['./verification-details.component.scss']
})
export class VerificationDetailsComponent extends BaseComponent {

  @Input() reviewId: number | null = null

  verification: IReviewVerification | null = null

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private reviewService: ReviewService,
    private auth: AuthService,
    private notifier: NotifierService
  ) {
    super(eventBus, router, subContainer)
  }

  protected componentName(): string {
    return "VerificationDetailsComponent";
  }

  ngOnInit(): void {
    this.fetchVerificationForReview()
  }

  private fetchVerificationForReview() {
    if (this.reviewId == null) return
    this.loading = true
    let key = this.componentKey(EventKey.REVIEWS.GET_VERIFICATION_FOR_REVIEW(this.reviewId))
    this.reviewService.getVerificationForReview(key, this.reviewId)
    this.subscribe(key, null, (value, meta) => {
      this.verification = meta.data
      console.log(this.verification)
      this.loading = false
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
      this.loading = false
    })
  }

  isVerified(): boolean {
    return this.verification?.status === 'VERIFIED'
  }

  isReconsiderable(): boolean {
    return !this.verification?.already_reconsidered && this.verification?.status === 'REJECTED'
  }

  applyForReconsideration() {
    if (this.verification == null) return
    if (!confirm("Do you want to send reconsideration request?")) return;
    let key = this.componentKey(EventKey.REVIEWS.RECONSIDER_VERIFICATION(this.verification.id))
    this.reviewService.reconsiderVerification(key, this.verification.id)
    this.subscribe(key, null, (value, meta) => {
      this.verification = meta.data
      this.notifier.success("Success", "Request sent for reconsideration.")
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error("Error", "Could not send request for reconsideration.")
    })
  }
}
