import {Component, Input, OnInit} from '@angular/core';
import {BaseComponent} from "../../../../base/components/base.component";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";
import {PostService} from "../../services/post.service";
import {IPost} from "../../models/posts/IPost";
import {EventKey} from "../../../../services/eventbus/event.key";
import {PostBoxComponent} from "../../components/social/post-box/post-box.component";
import {MatDialog} from "@angular/material/dialog";

@Component({
  selector: 'app-post-details',
  templateUrl: './post-details.component.html',
  styleUrls: ['./post-details.component.scss']
})
export class PostDetailsComponent extends BaseComponent {

  @Input() postId: number = 0
  @Input() post: IPost | null = null

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    route: ActivatedRoute,
    private postService: PostService,
    private dialog: MatDialog
  ) {
    super(eventBus, router, subContainer);
    this.postId = route.snapshot.params['postId']
  }

  protected componentName(): string {
    return "PostDetailsComponent";
  }

  ngOnInit(): void {
    this.loadData(this.postId)
  }

  private loadData(postId: number) {
    this.loading = true

    const key = this.componentKey(EventKey.POSTS.FETCH_POST(postId))
    this.postService.fetchPost(key, this.postId)
    this.subscribe(key, null, (value, meta) => {
      this.post = meta.data
      this.loading = false
    })

  }

  openPostBox(post: IPost | null) {
    const dialogRef = this.dialog.open(PostBoxComponent, {disableClose: true});
    let instance = dialogRef.componentInstance
    instance.commentBox = false
    instance.post = post
    instance.onPosted.subscribe((post: IPost) => {
      this.post = post
      dialogRef.close()
    })
    instance.onCancel.subscribe((close: boolean) => {
      dialogRef.close()
    })
  }

}
