import {Injectable} from '@angular/core';
import {WebAppEndpoint} from "../web-app.endpoint";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {BaseService} from "../../../base/services/base.service";
import {AuthService} from "../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../services/notifier/notifier.service";
import {EventBusService} from "../../../services/eventbus/event-bus.service";
import {IReportCategory} from "../models/posts/post-reports/report-category.model";
import {IPage} from "../../../base/models/IPage";
import {IPostReportRequest} from "../models/posts/post-reports/post-report-req.model";
import {IPostReport} from "../models/posts/post-reports/post-report.model";


@Injectable({
  providedIn: 'root'
})
export class PostReportService extends BaseService {

  constructor(authService: AuthService,
              notifier: NotifierService,
              private http: HttpClient,
              private eventBus: EventBusService) {
    super(authService, notifier);
  }

  reportReview(key: string, reportReq: IPostReportRequest) {
    this.http.post<IPostReport>(WebAppEndpoint.POSTS.POST_REVIEW_REPORT(), reportReq)
      .subscribe((report: IPostReport) => {
        this.eventBus.publish(key, report)
      }, (err: HttpErrorResponse) => {
        this.eventBus.publish(this.getErrorKey(key), err)
      })
  }

  getReportCategories(): IReportCategory[] {
    return [
      {name: 'NUDITY OR SEXUAL ACTIVITY', value: 'NUDITY_OR_SEXUAL_ACTIVITY'},
      {name: 'HARASSMENT OR BULLYING', value: 'HARASSMENT_OR_BULLYING'},
      {name: 'OFFENSIVE', value: 'OFFENSIVE'},
      {name: 'FALSE INFORMATION', value: 'FALSE_INFORMATION'},
      {name: 'SPAM', value: 'SPAM'},
      {name: 'VIOLENCE', value: 'VIOLENCE'},
      {name: 'HATE SPEECH', value: 'HATE_SPEECH'},
    ]
  }

  getReportsForPost(key: string, postId: number) {
    this.http.get<IPage<IPostReport>>(WebAppEndpoint.POSTS.GET_REPORT_DETAILS_FOR_POST(postId))
      .subscribe((reports: IPage<IPostReport>) => {
        this.eventBus.publish(key, reports)
      }, (err: HttpErrorResponse) => {
        this.eventBus.publish(this.getErrorKey(key), err)
      })
  }


  getReportCountForPost(key: string, postId: number) {
    this.http.get<number>(WebAppEndpoint.POSTS.GET_REPORT_COUNT_FOR_POST(postId))
      .subscribe((reportCount: number) => {
        this.eventBus.publish(key, reportCount)
      }, (err: HttpErrorResponse) => {
        this.eventBus.publish(this.getErrorKey(key), err)
      })
  }


}
