import {Params} from "@angular/router";
import * as moment from "moment";
import {TimeUtility} from "../../time.utility";
import {IParams} from "../../../../../../base/endpoints/iparams";

export interface FilterField {
  type: 'text' | 'number' | 'date' | 'dropdown';
  label: string;
  name: string;
  value: string | number | boolean | Date | null;
  placeholder?: string;
  options?: { label: string; value: string | number | boolean | Date }[];
}

export interface BasicFilterData {
  query: string | null
  fromDate: Date
  toDate: Date
  username: string | null
  page: number
  size: number
  extra: IParams
}

export const defaultFilterData: BasicFilterData = {
  query: null,
  fromDate: TimeUtility.getDayStart(new Date(0)),
  toDate: TimeUtility.getDayEnd(new Date()),
  username: null,
  page: 0,
  size: 10,
  extra: {}
}

export function defaultFilterDataWithExtra(extra: IParams): BasicFilterData {
  return {
    ...defaultFilterData,
    extra: extra
  }
}

export function toParams(filterData: BasicFilterData): IParams {
  return {
    query: filterData.query,
    username: filterData.username,
    from_date: filterData.fromDate.toISOString(),
    to_date: filterData.toDate.toISOString(),
    page: filterData.page,
    size: filterData.size,
    ...filterData.extra
  }
}

/*
* Parses the query params received from url to BasicFilterData object.
* @param params: Params object received from ActivatedRoute
* @param defaultSize: default size of the page
* @param extra: extra filter fields
 */
export function parseFilterData(params: Params, defaultSize: number = 10, extra: IParams={}): BasicFilterData {
  let query = params['query'] as string
  let username = params['username'] as string
  let fromDate: Date = new Date(0)
  let toDate: Date = new Date()

  if (params['from_date'] != null && params['to_date'] != null) {
    fromDate = moment(params['from_date']).toDate();
    toDate = moment(params['to_date']).toDate();
  }

  return {
    query: query,
    fromDate: fromDate,
    toDate: toDate,
    username: username != null && username.length > 0 ? username : null,
    page: params['page'] ? params['page'] : 0,
    size: params['size'] ? params['size'] : defaultSize,
    extra: convert(extra, params, [
      'query', 'username', 'from_date', 'to_date', 'page', 'size'
    ])
  }
}

