function convert(existing: IParams, newParams: Params, exclusions: string[]): IParams {
  Object.keys(newParams).forEach((key) => {
    if (!exclusions.includes(key)) {
      const value = newParams[key];

      if (typeof value === 'string') {
        const parsedDate = Date.parse(value);
        if (!isNaN(parsedDate) && !/^\d+$/.test(value)) {
          existing[key] = new Date(parsedDate);
        } else if (value.toLowerCase() === 'true' || value.toLowerCase() === 'false') {
          existing[key] = value.toLowerCase() === 'true';
        } else if (!isNaN(Number(value))) {
          existing[key] = Number(value);
        } else {
          existing[key] = value;
        }
      } else {
        existing[key] = value;
      }
    }
  });

  return existing;
}
