import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-notice-view',
  templateUrl: './notice-view.component.html',
  styleUrls: ['./notice-view.component.scss']
})
export class NoticeViewComponent implements OnInit {
  @Input() step: number = 2

  @Input() img: string | null = null
  @Input() title: string = ""
  @Input() message: string = ""
  @Input() positiveBtnText: string | null = null
  @Input() negativeBtnTxt: string | null = null
  @Input() titleColor = '#44d62c'
  @Input() msgColor = '#FFFFFF'

  @Output() onPositiveBtn: EventEmitter<boolean> = new EventEmitter<boolean>()
  @Output() onNegativeBtn: EventEmitter<boolean> = new EventEmitter<boolean>()

  constructor() {
  }

  ngOnInit(): void {
  }

  onBtnClick(positive: boolean) {
    if (positive)
      this.onPositiveBtn.emit()
    else this.onNegativeBtn.emit()
  }
}
