import {Component, Input} from '@angular/core';
import {BaseComponent} from "../../../../../base/components/base.component";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {ReviewService} from "../../../services/review.service";
import {AuthService} from "../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {EventKey} from "../../../../../services/eventbus/event.key";
import {PostReportService} from "../../../services/post-report.service";
import {IPostReport} from "../../../models/posts/post-reports/post-report.model";
import {IPage} from "../../../../../base/models/IPage";

@Component({
  selector: 'app-report-details',
  templateUrl: './report-details.component.html',
  styleUrls: ['./report-details.component.scss']
})
export class ReportDetailsComponent extends BaseComponent {

  @Input() reviewId: number | null = null
  @Input() postId: number | null = null

  report: IPostReport | null = null

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private reviewService: ReviewService,
    private postReportService: PostReportService,
    private auth: AuthService,
    private notifier: NotifierService
  ) {
    super(eventBus, router, subContainer)
  }

  protected componentName(): string {
    return "ReportDetailsComponent";
  }

  ngOnInit(): void {
    this.fetchReportDetails()
  }

  private fetchReportDetails() {
    if (this.postId == null) return
    this.loading = true
    let key = this.componentKey(EventKey.REVIEWS.GET_REPORT_DETAILS_FOR_REVIEW(this.reviewId))
    this.postReportService.getReportsForPost(key, this.postId)
    this.subscribe(key, null, (value, meta) => {
      let reports: IPage<IPostReport> = meta.data
      if (!reports.empty)
        this.report = reports.content[0]
      this.loading = false
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
      this.loading = false
    })
  }

  hasReport(): boolean {
    return this.report != null
  }

}
