import {Injectable} from '@angular/core';
import {BaseEndpoint} from "../../base/endpoints/base.endpoint";
import {IParams} from "../../base/endpoints/iparams";
import {environment} from "../../../environments/environment";

@Injectable({
  providedIn: 'root'
})
export class AdminEndpoint extends BaseEndpoint {

  constructor() {
    super();
  }

  static CRUDEXAMPLES = {
    CRUDEXAMPLE_ENDPOINT: environment.base_url + '/api/v1/crudexamples',
    CRUDEXAMPLE_ENDPOINT_ADMIN: environment.base_url + '/api/v1/admin/crudexamples',
    SEARCH(endpoint: string, params: IParams) {
      return endpoint.concat(BaseEndpoint.toParamString(params));
    },
    CREATE(): string {
      return this.CRUDEXAMPLE_ENDPOINT
    },
    GET(id: number): string {
      return this.CRUDEXAMPLE_ENDPOINT + '/' + id
    },
    UPDATE(id: number): string {
      return this.CRUDEXAMPLE_ENDPOINT + '/' + id
    },
  }

  static LABELS = {
    LABEL_ENDPOINT_POLLBOX: environment.base_url + '/api/v1/public/labels/POLLBOX/children',
    LABEL_ENDPOINT: environment.base_url + '/api/v1/labels',
    LABEL_ENDPOINT_ADMIN: environment.base_url + '/api/v1/admin/labels',
    SEARCH(endpoint: string, params: IParams) {
      return endpoint.concat(BaseEndpoint.toParamString(params));
    },
    CREATE(): string {
      return this.LABEL_ENDPOINT
    },
    GET(id: number): string {
      return this.LABEL_ENDPOINT + '/' + id
    },
    GET_MULTIPLE(ids: number[]): string {
      return this.LABEL_ENDPOINT + '/multiple/by-ids?ids=' + ids.join(',')
    },
    UPDATE(id: number): string {
      return this.LABEL_ENDPOINT + '/' + id
    },
  }

  static FEEDBACKS = {
    FEEDBACK_ENDPOINT: environment.base_url + '/api/v2/feedbacks',
    FEEDBACK_ENDPOINT_ADMIN: environment.base_url + '/api/v2/admin/feedbacks',
    SEARCH(endpoint: string, params: IParams) {
      return endpoint.concat(BaseEndpoint.toParamString(params));
    },
    CREATE(): string {
      return this.FEEDBACK_ENDPOINT
    },
    GET(id: number): string {
      return this.FEEDBACK_ENDPOINT + '/' + id
    },
    UPDATE(id: number): string {
      return this.FEEDBACK_ENDPOINT + '/' + id
    },
    TAKE_ACTION(id: number): string {
      return this.FEEDBACK_ENDPOINT_ADMIN + '/' + id + '/action'
    }
  }

  static POPTIONS = {
    POPTION_ENDPOINT: environment.base_url + '/api/v1/poll-options',
    POPTION_ENDPOINT_ADMIN: environment.base_url + '/api/v1/admin/poll-options',
    SEARCH(endpoint: string, params: IParams) {
      return endpoint.concat(BaseEndpoint.toParamString(params));
    },
    CREATE(): string {
      return this.POPTION_ENDPOINT
    },
    GET(id: number): string {
      return this.POPTION_ENDPOINT + '/' + id
    },
    UPDATE(id: number): string {
      return this.POPTION_ENDPOINT + '/' + id
    },
  }

  static ACVALIDATIONTOKENS = {
    ACVALIDATIONTOKEN_ENDPOINT: environment.base_url + '/api/v1/validation-tokens',
    ACVALIDATIONTOKEN_ENDPOINT_ADMIN: environment.base_url + '/api/v1/admin/validation-tokens',
    SEARCH(endpoint: string, params: IParams) {
      return endpoint.concat(BaseEndpoint.toParamString(params));
    },
    CREATE(): string {
      return this.ACVALIDATIONTOKEN_ENDPOINT
    },
    GET(id: number): string {
      return this.ACVALIDATIONTOKEN_ENDPOINT + '/' + id
    },
    UPDATE(id: number): string {
      return this.ACVALIDATIONTOKEN_ENDPOINT + '/' + id
    },
  }

  static LOCATIONTYPES = {
    LOCATIONTYPE_ENDPOINT: environment.base_url + '/api/v1/location-types',
    LOCATIONTYPE_ENDPOINT_ADMIN: environment.base_url + '/api/v1/location-types',
    SEARCH(endpoint: string, params: IParams) {
      return endpoint.concat(BaseEndpoint.toParamString(params));
    },
    CREATE(): string {
      return this.LOCATIONTYPE_ENDPOINT
    },
    GET(id: number): string {
      return this.LOCATIONTYPE_ENDPOINT + '/' + id
    },
    GET_MULTIPLE(ids: number[]): string {
      return this.LOCATIONTYPE_ENDPOINT + '/multiple/by-ids?ids=' + ids.join(',')
    },
    UPDATE(id: number): string {
      return this.LOCATIONTYPE_ENDPOINT + '/' + id
    },
  }

  static LOCATIONS = {
    LOCATION_ENDPOINT: environment.base_url + '/api/v1/locations',
    LOCATION_ENDPOINT_V2: environment.base_url + '/api/v2/locations',
    LOCATION_ENDPOINT_ADMIN: environment.base_url + '/api/v1/locations',
    SEARCH(endpoint: string, params: IParams) {
      return endpoint + '/child-locations'
        .concat(BaseEndpoint.toParamString(params));
    },
    GET_LOCATIONS_FOR_TYPE(endpoint: string, id: number): string {
      return endpoint + '/' + id + '/locations'
    },
    CREATE(): string {
      return this.LOCATION_ENDPOINT
    },
    GET(id: number): string {
      return this.LOCATION_ENDPOINT + '/' + id
    },
    GET_MULTIPLE(ids: number[]): string {
      return this.LOCATION_ENDPOINT_V2 + '/multiple/by-ids?ids=' + ids.join(',')
    },
    UPDATE(id: number): string {
      return this.LOCATION_ENDPOINT + '/' + id
    },
  }

  static ADDRESSS = {
    ADDRESS_ENDPOINT: environment.base_url + '/api/v1/global-addresss',
    ADDRESS_ENDPOINT_V2: environment.base_url + '/api/v2/global-addresses',
    ADDRESS_ENDPOINT_ADMIN: environment.base_url + '/api/v1/global-addresss',
    SEARCH(endpoint: string, params: IParams) {
      return endpoint.concat(BaseEndpoint.toParamString(params));
    },
    CREATE(): string {
      return this.ADDRESS_ENDPOINT
    },
    GET(id: number): string {
      return this.ADDRESS_ENDPOINT_V2 + '/' + id
    },
    UPDATE(id: number): string {
      return this.ADDRESS_ENDPOINT + '/' + id
    },
  }
}
