import {Component, Input} from '@angular/core';
import {BaseComponent} from "../../../../base/components/base.component";
import {DatePipe, Location} from "@angular/common";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";
import {AuthService} from "../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../services/notifier/notifier.service";
import {UserService} from "../../services/user.service";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventKey} from "../../../../services/eventbus/event.key";
import {IProfile} from "../../models/users/profile.model";
import {HttpErrorResponse} from "@angular/common/http";
import {IProfileReq} from "../../models/users/profile-req.model";
import {IUploadFile} from "../../models/uploads/IUploadFile";
import {FileUploadService} from "../../../../services/files/file-upload.service";
import {IImageUploadResponse} from "../../../../models/IImageUploadResponse";

@Component({
  selector: 'app-update-profile',
  templateUrl: './update-profile.component.html',
  styleUrls: ['./update-profile.component.scss']
})
export class UpdateProfileComponent extends BaseComponent {

  @Input() userId: number | null = null

  profileForm = new FormGroup({
    gender: new FormControl('', [Validators.required]),
    birthday: new FormControl('', [Validators.required]),
    public: new FormControl(false),
    blood_group: new FormControl(''),
    marital_status: new FormControl(''),
    religion: new FormControl(''),
    use_photo_as_avatar: new FormControl('')
  })

  profile: IProfile | null = null

  uploadFiles: IUploadFile[] = []

  constructor(private location: Location,
              eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private notifier: NotifierService,
              private userService: UserService,
              private auth: AuthService,
              private datePipe: DatePipe,
              private fileUploadService: FileUploadService) {
    super(eventBus, router, subContainer);
  }

  protected componentName(): string {
    return "UpdateProfileComponent";
  }

  ngOnInit(): void {
    this.fetchMyProfile()
  }

  private fetchMyProfile() {
    this.loading = true
    let key = this.componentKey(EventKey.USERS.GET_MY_PROFILE())
    this.userService.getMyProfile(key)
    this.subscribe(key, null, (value, meta) => {
      this.profile = meta.data
      this.loading = false
      this.updateForm(this.profile)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let err: HttpErrorResponse = meta.data
      if (err.error.code == 404) {
        this.loading = false
        this.notifier.info('No profile', "Fill the form to create your profile ")
      }
      this.notifier.log(err)
    })
  }

  updateProfile() {
    let profileReq: IProfileReq = this.mapToProfile(this.profileForm.value)
    let key = this.componentKey(EventKey.USERS.UPDATE_MY_PROFILE())
    this.userService.updateProfile(key, profileReq)
    this.subscribe(key, null, (value, meta) => {
      this.profile = meta.data
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.log(meta.data)
    })
  }

  private mapToProfile(value: any): IProfileReq {
    return {
      gender: value.gender,
      birthday: new Date(value.birthday),
      blood_group: value.blood_group === "" ? null : value.blood_group,
      marital_status: value.marital_status === "" ? null : value.marital_status,
      religion: value.religion === "" ? null : value.religion,
      public: value.public !== "",
      use_photo_as_avatar: true,
      photo: this.profile?.photo == null ? null : this.profile.photo,
      user_id: this.auth.getAuth().id
    };
  }

  private updateForm(profile: IProfile | null) {
    if (profile == null) return
    this.profileForm.controls['public'].setValue(profile.public)
    this.profileForm.controls['birthday'].setValue(this.datePipe.transform(profile.birthday, 'yyyy-MM-dd'))
    this.profileForm.controls['gender'].setValue(profile.gender)
    this.profileForm.controls['marital_status'].setValue(profile.marital_status)
    this.profileForm.controls['blood_group'].setValue(profile.blood_group)
    this.profileForm.controls['religion'].setValue(profile.religion)
  }

  hasProfilePhoto(): boolean {
    return this.profile?.photo != null && this.profile?.photo.trim() !== ''
  }

  onImageSelected(event: any) {
    if (this.uploadFiles.length >= 3) {
      this.notifier.info('Operation not allowed!', 'You can upload 3 photos in a post.')
      return;
    }
    if (event.target.files && event.target.files[0]) {
      // validate image
      const mimeType = event.target.files[0].type;
      if (mimeType.match(/image\/*/) == null) {
        this.notifier.warning("Unsupported", "Only images are supported.");
        return;
      }

      let uf: IUploadFile = {
        file: event.target.files[0],
        previewUrl: null
      }

      const reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (e) => { // called once readAsDataURL is completed
        let url = e.target?.result
        if (url !== undefined && url !== null) {
          uf.previewUrl = url
          this.uploadFiles.push(uf);
          this.uploadPhoto()
        }
      }
    }
  }

  private uploadPhoto() {
    this.loading = true
    let key = this.componentKey(EventKey.FILES.IMAGE_UPLOAD(this.userId + ''))
    this.fileUploadService.uploadImages(key, this.uploadFiles.map(f => f.file))
    this.subscribe(key, null, (value, meta) => {
      this.loading = false
      let imgs: IImageUploadResponse[] = meta.data
      if (this.profile != null)
        this.profile.photo = imgs[0].thumb_url
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.loading = false
      this.notifier.error("Error", meta.data.error.message)
    })
  }
}
