import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {OwlOptions} from "ngx-owl-carousel-o";
import {ICarouselItem} from "../../models/ICarouselItem";
import {BaseComponent} from "../../../../base/components/base.component";
import {EventBusService} from "../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../base/services/subscription-container.service";

@Component({
  selector: 'app-small-carousel',
  templateUrl: './small-carousel.component.html',
  styleUrls: ['./small-carousel.component.scss']
})
export class SmallCarouselComponent extends BaseComponent {
  carouselOptions: OwlOptions = {
    loop: true,
    items: 4,
    smartSpeed: 500,
    autoplay: false,
    autoWidth: true,
    margin: 10,
    nav: false,
    dots: false,
    responsive: {
      0: {
        items: 5
      },
      480: {
        items: 5
      },
      600: {
        items: 7
      },
      800: {
        items: 8
      },
      1200: {
        items: 10
      }
    }
  }

  @Input() items: ICarouselItem[] = []
  @Input() design: string = 'reviewers'
  @Output() itemClick: EventEmitter<ICarouselItem> = new EventEmitter<ICarouselItem>()

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService
  ) {
    super(eventBus, router, subContainer);
  }

  protected componentName(): string {
    return "SmallCarouselComponent";
  }

  ngOnInit(): void {
    // console.log(this.items)
  }

  onItemClick(item: ICarouselItem) {
    this.itemClick.emit(item)
  }
}
