import {Component, Input, OnInit} from '@angular/core';
import {IPost} from "../../../models/posts/IPost";
import {IPage} from "../../../../../base/models/IPage";
import {BaseComponent} from "../../../../../base/components/base.component";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../base/services/subscription-container.service";
import {PostService} from "../../../services/post.service";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {EventKey} from "../../../../../services/eventbus/event.key";

@Component({
  selector: 'app-conversation-area',
  templateUrl: './conversation-area.component.html',
  styleUrls: ['./conversation-area.component.scss']
})
export class ConversationAreaComponent extends BaseComponent {
  @Input() postId: number | null | undefined = null
  comments: IPost[] = []
  latestCommentId: number | null | undefined

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    route: ActivatedRoute,
    private postService: PostService,
    private notifier: NotifierService
  ) {
    super(eventBus, router, subContainer);
  }

  protected componentName(): string {
    return "ConversationAreaComponent";
  }

  ngOnInit(): void {
    this.notifier.log('post id: ' + this.postId)
    if (this.postId != null) {
      this.loadComments(this.postId)
    }
  }


  private loadComments(postId: number) {
    const key = this.componentKey(EventKey.POSTS.FETCH_COMMENTS(postId))
    this.postService.fetchComments(key, postId)
    this.subscribe(key, null, (value, meta) => {
      const commentPage: IPage<IPost> = meta.data
      this.comments = commentPage.content
      this.loading = false
    })
  }

  onCommentSubmitted(comment: IPost) {
    this.latestCommentId = comment.id
    this.comments.unshift(comment)
    // console.log(this.comments)
    // this.notifier.success('Submitted!', "You comment has been successfully submitted.")
  }
}
